import echarts from 'echarts'
const install = function(Vue) {
  Object.defineProperties(Vue.prototype, {
    $chart: {
      get() {
        return {
            //画一条简单的线
            line1: function (id) {
                this.chart = echarts.init(document.getElementById(id));
                this.chart.clear();
                const optionData = {
                    xAxis: {
                        type: 'category',
                        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [820, 932, 901, 934, 1290, 1330, 1320],
                        type: 'line',
                        smooth: true
                    }]
                };

                this.chart.setOption(optionData);
            },
            //多柱状图
            bar1: function (id,len_data,x_data,y_data) {
              this.chart = echarts.init(document.getElementById(id));
              this.chart.clear();
              var colors = ['#5793f3', '#d14a61', '#675bba','#5d8c76', '#d0d83b', '#675bba','#5793f3', '#d14a61', '#675bba'];
              let option1 = {
                color: colors,
                tooltip: {
                  trigger: 'axis',
                  axisPointer: {
                    type: 'cross'
                  }
                },
                grid: {
                  right: '20%'
                },
                toolbox: {
                  feature: {
                    dataView: {show: true, readOnly: false},
                    magicType: {show: true, type: ['line', 'bar']},
                    restore: {show: true},
                    saveAsImage: {show: true}
                  }
                },
                legend: {
                  data:len_data
                },
                xAxis: [
                  {
                    type: 'category',
                    axisTick: {
                      alignWithLabel: true
                    },
                    data: x_data
                  }
                ],
                yAxis: [
                    {
                      type: 'value',
                      name: len_data[0],
                      position: 'right',
                      axisLine: {
                        lineStyle: {
                          color: colors[0]
                        }
                      },
                      axisLabel: {
                        formatter: '{value} '
                      }
                    },
                    {
                      type: 'value',
                      name: len_data[1],
                      position: 'right',
                      offset: 80,
                      axisLine: {
                        lineStyle: {
                          color: colors[1]
                        }
                      },
                      axisLabel: {
                        formatter: '{value} '
                      }
                    },
                    {
                      type: 'value',
                      name: len_data[2],
                      position: 'left',
                      offset: 80,
                      axisLine: {
                        lineStyle: {
                          color: colors[2]
                        }
                      },
                      axisLabel: {
                        formatter: '{value} '
                      }
                    },
                    {
                      type: 'value',
                      name: len_data[3],
                      position: 'right',
                      offset: 160,
                      axisLine: {
                        lineStyle: {
                          color: colors[3]
                        }
                      },
                      axisLabel: {
                        formatter: '{value} '
                      }
                    },
                    {
                      type: 'value',
                      name: len_data[4],
                      position: 'left',
                      axisLine: {
                        lineStyle: {
                          color: colors[4]
                        }
                      },
                      axisLabel: {
                        formatter: '{value}'
                      }
                    }
                ],
                series: [
                  {
                    name:len_data[0],
                    type:'bar',
                    data:y_data[0]
                  },
                  {
                    name:len_data[1],
                    type:'bar',
                    yAxisIndex: 1,
                    data:y_data[1]
                  },
                  {
                    name:len_data[2],
                    type:'bar',
                    yAxisIndex: 4,
                    data:y_data[2]
                  },
                  {
                    name:len_data[3],
                    type:'bar',
                    yAxisIndex: 2,
                    data:y_data[3]
                  },
                  {
                    name:len_data[4],
                    type:'line',
                    yAxisIndex: 3,
                    data:y_data[4]
                  }
                ]
              };
              this.chart.setOption(option1);
            },
            //多柱状图
            bar3: function (id,len_data,x_data,y_data) {
              this.chart = echarts.init(document.getElementById(id));
              this.chart.clear();
              var colors = ['#5793f3', '#d14a61', '#675bba','#5d8c76', '#d0d83b', '#675bba','#5793f3', '#d14a61', '#675bba'];
              let option1 = {
                color: colors,
                tooltip: {
                  trigger: 'axis',
                  axisPointer: {
                    type: 'cross'
                  }
                },
                grid: {
                  right: '20%'
                },
                toolbox: {
                  feature: {
                    dataView: {show: true, readOnly: false},
                    magicType: {show: true, type: ['line', 'bar']},
                    restore: {show: true},
                    saveAsImage: {show: true}
                  }
                },
                legend: {
                  data:len_data
                },
                xAxis: [
                  {
                    type: 'category',
                    axisTick: {
                      alignWithLabel: true
                    },
                    data: x_data
                  }
                ],
                yAxis: [
                  {
                    type: 'value',
                    name: len_data[0],
                    position: 'right',
                    axisLine: {
                      lineStyle: {
                        color: colors[0]
                      }
                    },
                    axisLabel: {
                      formatter: '{value} '
                    }
                  },
                  {
                    type: 'value',
                    name: len_data[1],
                    position: 'right',
                    offset: 80,
                    axisLine: {
                      lineStyle: {
                        color: colors[1]
                      }
                    },
                    axisLabel: {
                      formatter: '{value} '
                    }
                  },
                  {
                    type: 'value',
                    name: len_data[2],
                    position: 'left',
                    axisLine: {
                      lineStyle: {
                        color: colors[2]
                      }
                    },
                    axisLabel: {
                      formatter: '{value}'
                    }
                  },
                  {
                    type: 'value',
                    name: len_data[3],
                    position: 'left',
                    offset: 80,
                    axisLine: {
                      lineStyle: {
                        color: colors[3]
                      }
                    },
                    axisLabel: {
                      formatter: '{value} '
                    }
                  },
                ],
                series: [
                  {
                    name:len_data[0],
                    type:'bar',
                    data:y_data[0]
                  },
                  {
                    name:len_data[1],
                    type:'bar',
                    yAxisIndex: 1,
                    data:y_data[1]
                  },
                  {
                    name:len_data[2],
                    type:'bar',
                    yAxisIndex: 2,
                    data:y_data[2]
                  },
                  {
                    name:len_data[3],
                    type:'bar',
                    yAxisIndex: 3,
                    data:y_data[3]
                  },
                ]
              };
              this.chart.setOption(option1);
            },
            // 单柱状图
            bar2:function(id,data_x,data_y,color){
              this.chart = echarts.init(document.getElementById(id));
              this.chart.clear();
              var option2= {
                color: color,
                tooltip : {
                  trigger: 'axis',
                  axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                  }
                },
                grid: {
                  left: '3%',
                  right: '4%',
                  bottom: '3%',
                  containLabel: true
                },
                xAxis : [
                  {
                    type : 'category',
                    data : data_x,
                    axisTick: {
                      alignWithLabel: true
                    }
                  }
                ],
                yAxis : [
                  {
                    type : 'value'
                  }
                ],
                series : [
                  {
                    name:'',
                    type:'bar',
                    barWidth: '60%',
                    data:data_y,
                  }
                ]
              };
              this.chart.setOption(option2);
            }
        }
      }
    }
  })
}

export default {
    install
}



