<template lang="">
    <div id='shopp_over'>
      <!-- content -->
      <div>
        <!-- one -->
        <div class="bob mg-10 bg-fff">
          <div class="fsa padd-tb-20 txalS white">
            <div class="box bob padd-30" style="background:url('../../../static/reset/goods_over/bg_one.png');">
              <p class='f14 mg-b-14'>商品数量（种）  </p>
              <p class=' f32'><span class='f12'>￥</span>{{StoreData.goods_num}}</p>
            </div>
            <div class="box bob padd-30" style="background:url('../../../static/reset/goods_over/bg_two.png');">
              <p class='f14 mg-b-14'>商品库存（件）  </p>
              <p class=' f32'><span class='f12'>￥</span>{{StoreData.repertory_sum}}</p>
            </div>
            <div class="box bob padd-30" style="background:url('../../../static/reset/goods_over/bg_three.png');">
              <p class='f14 mg-b-14'>库存不足数量（种）  </p>
              <p class=' f32'><span class='f12'>￥</span>{{StoreData.repertory_warning}}</p>
            </div>
          </div>
        </div>
          <!-- two header -->
          <HeaderChaqnge v-show='false' @headerC='getheaderC1' @chart='chart1' :title='title1'></HeaderChaqnge>
          <!-- two content -->
          <div class='' v-show='false'>
            <h6 class='txalS mg-20'>商品种类</h6>
            <hr class='rel' style='top:178px;' />
            <div class='fsa'>
              <div class='fdc aic'>
                <i class='dslb bg-gray' style='width:50px;height:160px;'></i>
                <p class='dslb mg-10'>商品销售</p>
              </div>
              <div class='fdc aic'>
                <i class='dslb bg-gray' style='width:50px;height:160px;'></i>
                <p class='dslb mg-10'>客户退货</p>
              </div>
              <div class='fdc aic'>
                <i class='dslb bg-gray' style='width:50px;height:160px;'></i>
                <p class='dslb mg-10'>库存盘点</p>
              </div>
              <div class='fdc aic'>
                <i class='dslb bg-gray' style='width:50px;height:160px;'></i>
                <p class='dslb mg-10'>编辑库存</p>
              </div>
            </div>
          </div>
          <div class='bob bg-fff mg-10'>
            <!-- three header -->
            <div class="fsb padd-20 bg-fff">
              <h3 class='fsb'><i class='bg_box dslb'></i><span>商品警惕</span></h3>
              <div class="fsa blue_0 f14" style='border:1px solid rgba(0,102,255,1)'>
                <span class="padd-rl-12 te_de dslb" :class="[index_top1==0?'blue_box':'']"  v-on:click="change1(0)">销量下跌</span>
                <span class="padd-rl-12 te_de dslb" :class="[index_top1==1?'blue_box':'']"  v-on:click="change1(1)">滞销商品</span>
              </div>
            </div>
            <!-- three content-1 -->
            <div class='' v-show='index_top1==0'>
              <p class='gray txalS  mg-10'>销量下跌是指最近七天销量较上一个周期销量下跌的商品</p>
              <!-- table -->
              <Table :tableData='tableData' :table_title='table_title' :proplist='proplist' ></Table>
              <PageCoat @getpage='change1getpage' :total='change1_total'></PageCoat>
            </div>
            <!-- three content-2 -->
            <div class='' v-show='index_top1==1'>
              <p class='gray txalS mg-10'>滞销商品是指最近3个月有销量，但最近7天没有产生任何销量的商品</p>
              <!-- table -->
              <Table :tableData='tableData' :table_title='table_title' :proplist='proplist' ></Table>
              <PageCoat @getpage='change2getpage' :total='change2_total'></PageCoat>
            </div>
          </div>
          <div class='bob mg-10 bg-fff'>
            <!-- fore header -->
            <HeaderChaqnge @headerC='getheaderC2' @chart='chart2' :title='title2' class='mg-t-10'></HeaderChaqnge>
            <!-- fore content-1 -->
            <div class=''>
              <div class='fsb mg-10'>
                <p><i class='dslb bg-gray' style='width:5px;height:16px;background:#908080;'></i><span class='gray'>商品分类消费排名</span></p>
                <p><span class='dslb gray padd-5 bor_le_66 mg-rl-5' :style="{background:index_store_top==0?'orange':''}" v-on:click="store('')">销量</span><span v-on:click="store(1)" :style="{background:index_store_top==1?'orange':''}" class=' mg-rl-5 dslb bor_le_66 gray padd-5'>金额</span></p>
              </div>
              <!-- table -->
              <Table :tableData='tableData1' :table_title='table_title1' :proplist='proplist1'></Table>
            </div>
            <!-- fore content-2 -->
            <div class=''>
              <div class='fsb mg-10'>
                <p><i class='dslb bg-gray' style='width:5px;height:16px;background:#908080;'></i><span class='gray'>普通商品销售排名</span></p>
                <p><span class='dslb gray padd-5 bor_le_66 mg-rl-5' :style="{background:index_store1_top==0?'orange':''}" v-on:click="store1('')">销量</span><span v-on:click="store1(1)" :style="{background:index_store1_top==1?'orange':''}" class=' mg-rl-5 dslb bor_le_66 gray padd-5'>金额</span></p>
              </div>
              <!-- table -->
              <Table :tableData='tableData2' :table_height='table_height' :table_title='table_title2' :proplist='proplist2'></Table>
              <!-- <PageCoat @getpage='cgetpage' :total='c_total'></PageCoat> -->
            </div>
          </div>

      </div>
    </div>

</template>

<script>
import Table from '../../components/table';
import HeaderChaqnge from '../../components/headerChange.vue';
import PageCoat from '../../components/pagination';
export default {
  name:'index',
  components:{
    HeaderChaqnge,Table,PageCoat
  },
  data(){
    return {
      title1:'库存变动',
      title2:'销售分析',
      v_flag:true,
      index_top1:0,
      index_store_top:'',
      index_store1_top:'',
      tableData: [],
      proplist:['goods_name','goods_number','future_goods_number','spec'],
      table_title:['商品名称','上周7天销量','最近7天销量','下跌比例'],
      is_today:true,
      is_yesterday:false,
      is_week:false,
      is_month:false,
      tableData1: [],
      table_title1:['排名','商品分类','销量（个）','占比','同比','环比'],
      table_title2:['排名','商品名称','销量（个）','占比','同比','环比'],
      proplist1:['index','name','goods_number','zb','tb','hb'],
      proplist2:['index','goods_name','goods_number','zb','tb','hb'],
      tableData2: [],
      table_height:'500',
      StoreData:{},
      change1_total:0,
      change2_total:0,
      change1_onepage:10,
      change1_pagenow:1,
      value1:[],
      solt_1:'',
      solt_2:'',
    }
  },
  created() {
    this.getstoreData()
    this.getStagnateGoodsList()
    this.getDowngoodsList()
    this.getdata()
    this.getdata1()
  },
  mounted() {
  },
  methods: {
    // 店铺信息
    getstoreData(){
      let data={}
      this.$api.getStoreData(data,res=>{
        console.log(res)
        this.StoreData=res.data[0]
      })
    },
    // 商品滞销
    getStagnateGoodsList(){
      let data={
        page_no:this.change1_pagenow,
        record_no:this.change1_onepage,
        is_order_by_money:''
      }
      this.$api.getStagnateGoodsList(data,res=>{
        if(res.data.length==0){return false}
        this.change2_total=res.data[0].filter.total_number
        console.log(res)
        this.getStagnateGoodsListD=res.data[0].list
        this.change1_cha()
      })
    },
    // 销量下跌
    getDowngoodsList(){
      let data={
        page_no:this.change1_pagenow,
        record_no:this.change1_onepage,
        is_order_by_money:''
      }
      this.$api.getDowngoodsList(data,res=>{
        if(res.data.length==0){return false}
        this.change1_total=res.data[0].filter.total_number
        this.tableData=res.data[0].list
        this.getDowngoodsListD=res.data[0].list
        for(let index in this.tableData){
          this.tableData[index].spec=((this.tableData[index].goods_number-this.tableData[index].future_goods_number)/this.tableData[index].goods_number*100).toFixed(0)+'%'
        }
        this.change1_cha()
      })
    },
    getheaderC1(data){
      console.log('11',data)
    },
    chart1(data){
      console.log('11',data)
    },
    // 分类排名
    getdata(){
      console.log(this.value1[0])
      console.log(this.value1[1])
      let start_date_g=''
      start_date_g=this.value1[0]
      let data={
        start_date:this.value1.length>0?this.value1[0]:'',
        end_date:this.value1.length>0?this.value1[1]:'',
        is_today:this.is_today?1:2,
        is_yesterday:this.is_yesterday?1:2,
        is_week:this.is_week?1:2,
        is_month:this.is_month?1:2,
        type:'1',
        is_order_by_money:this.index_store_top,
      }
      this.$api.getSalescomposeanalysisList(data,res=>{
        console.log(res)
        if(res.data.length==0){return false}
        this.tableData1=res.data[0].list.table
        for(let index in this.tableData1){
          this.tableData1[index].index=Number(index)+1
        }
      })
    },
    // 普通排名
    getdata1(){
      let data={
        start_date:this.value1.length>0?this.value1[0]:'',
        end_date:this.value1.length>0?this.value1[1]:'',
        is_today:this.is_today?1:2,
        is_yesterday:this.is_yesterday?1:2,
        is_week:this.is_week?1:2,
        is_month:this.is_month?1:2,
        is_order_by_money:this.index_store1_top,
        goods_name:'',
        goods_sn:'',
      }
      this.$api.getSalesreviewList(data,res=>{
        console.log(res)
       if(res.data.length==0){return false}
        this.tableData2=res.data[0].list.table
        for(let index in this.tableData2){
          this.tableData2[index].index=Number(index)+1
        }
      })
    },
    getheaderC2(data){
      console.log('22',data)
      switch (data) {
      case 0:
        this.is_today=true;
        this.is_yesterday=false;
        this.is_week=false;
        this.is_month=false;
        this.value_date=[]
        this.getdata()
        this.getdata1()
        break;
      case 1:
        this.is_yesterday=true;
        this.is_today=false;
        this.is_week=false;
        this.is_month=false;
        this.value_date=[]
        this.getdata()
        this.getdata1()
        break;
      case 2:
        this.is_week=true;
        this.is_yesterday=false;
        this.is_today=false;
        this.is_month=false;
        this.value_date=[]
        this.getdata()
        this.getdata1()
        break;
      case 3:
        this.is_month=true;
        this.is_week=false;
        this.is_yesterday=false;
        this.is_today=false;
        this.value_date=[]
        this.getdata()
        this.getdata1()
        break;
      case 4:
        this.is_today=false;
        this.is_yesterday=false;
        this.is_week=false;
        this.is_month=false;
        this.value_date=[]
        break;
      default:
        break;
      }
    },
    chart2(data){
      console.log('22',data)
      this.value1=data.value1
      this.getdata()
      this.getdata1()
    },
    // 商品警惕切换1
    change1(a){
      this.index_top1=a
      this.change1_cha()
    },
    // 商品警惕切换2
    change1_cha(){
      if(this.index_top1==0){
        this.proplist=['goods_name','goods_number','future_goods_number','spec']
        this.table_title=['商品名称','上周7天销量','最近7天销量','下跌比例']
        this.tableData=this.getDowngoodsListD
      }else{
        this.proplist=['goods_name','goods_number','future_goods_number_7']
        this.table_title=['商品名称','最近3月销量','最近7天销量']
        this.tableData=this.getStagnateGoodsListD
      }
    },
    store(a){
      this.index_store_top=a
      this.getdata()
    },
    store1(a){
      this.index_store1_top=a
      this.getdata1()
    },
    change1getpage(data){
      switch (data.type) {
        case 0:
          this.onepage=data.val
          this.getDowngoodsList()
          break;
        case 1:
          this.pagenow=data.val
          this.getDowngoodsList()
          break;
        default:
          break;
      }
    },
    change2getpage(data){
      switch (data.type) {
        case 0:
          this.change1_onepage=data.val
          this.getStagnateGoodsList()
          break;
        case 1:
          this.change1_pagenow=data.val
          this.getStagnateGoodsList()
          break;
        default:
          break;
      }
    },
  },
}
</script>

<style lang="less" scoped>
#shopp_over{
  .box{
    width:341px;
    height:124px;
    // background:linear-gradient(0deg,rgba(0,102,255,1),rgba(146,190,255,1));
    border-radius:14px;
  }
  .bg_box{
    width:5px;
    height:18px;
    background:rgba(0,102,255,1);
    border-radius:2px;
    margin-right: 6px;
  }
  .blue_box{
    width:58px;
    height:25px;
    line-height: 25px;
    background:rgba(0,102,255,1);
    color: white;
  }
}

</style>




