<template>
  <div id="member_once_box">
    <!-- header -->
    <div class="mg-10">
      <h5 class="dslb">日期选择：</h5>
      <el-button @click="chosedate(1)" :style="{background:num==1?'skyblue':'',color:num==1?'white':''}">近一周</el-button>
      <el-button @click="chosedate(2)" :style="{background:num==2?'skyblue':'',color:num==2?'white':''}">近两周</el-button>
      <el-button @click="chosedate(3)" :style="{background:num==3?'skyblue':'',color:num==3?'white':''}">近一月</el-button>
      <el-button type="primary" v-on:click='chosedate(4)'>+</el-button>
      <div v-if="isdate" class="dslb">
        <el-date-picker
          v-model="value1"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期">
        </el-date-picker>
      </div>
    </div>
    <!-- header 2 -->
    <div class="mg-10">
       <h5 class="dslb">客单区间：</h5>
       <!-- <input v-model="input3" placeholder="" style="width:100px;"> -->
        <el-input-number v-model="input3" controls-position="right" @change="handleChange1" :min="0" :max="99999999"></el-input-number>
        <button>元</button>
        ——
        <!-- <input v-model="input4" placeholder="" style="width:100px;"> -->
        <el-input-number v-model="input4" controls-position="right" @change="handleChange2" :min="0" :max="99999999"></el-input-number>
        <button>元</button>
        <h6 class="dslb">均分</h6>
        <!-- <input v-model="input5" placeholder="" type="number" min='0' max="10" style="width:100px;"> -->
        <el-input-number v-model="input5" controls-position="right" :min="1" :max="10"></el-input-number>
        <button>段</button>
        <span>最多选取10段</span>
    </div>
    <!-- header 3 -->
    <div class="txalE mg-15">
      <EXport :ex_data='ex_data' class="dslb"></EXport>
      <el-button type="warning" @click="getdata">查询</el-button>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
  </div>
</template>

<script>
import Table from '../../../components/table';
import EXport from '../../../components/export';
export default {
  components:{
    Table,EXport
  },
  data() {
    return {
      isdate:false,
      value1:'',
      input3:0,
      input4:0,
      input5:1,
      tableData: [],
      table_title:['序号','客单价（元）','会员数','占比'],
      proplist:['index','range','num','propor'],
      table_height:'500',
      ex_data:{},
      num:1,
    }
  },
  watch: {
    'tableData':function (n,o) {
      this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'会员客单价分析'}
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    chosedate(num){
      switch (num) {
        case 1:
          this.num=num
          this.value1=''
          this.isdate=false;
          break;
        case 2:
          this.num=num
          this.value1=''
          this.isdate=false;
          break;
        case 3:
          this.num=num
          this.value1=''
          this.isdate=false;
          break;
        case 4:
          this.num=num
          this.isdate=true;
          break;
        default:
          break;
      }
      console.log(this.num)
    },
    getdata(){
      let data={
        one_week:this.num==1?1:2,
        two_weeks:this.num==2?1:2,
        one_month:this.num==3?1:2,
        start_date:this.value1?this.value1[0]:'',
        end_date:this.value1?this.value1[1]:'',
        min:this.input3,
        max:this.input4,
        num:this.input5,
      }
      this.$api.memberPerorderAnalysis(data,res=>{
        console.log(res)
        this.totle_num=res.data[0].filter.total_number
        this.tableData=res.data[0].list
        for(let index in this.tableData){
          this.tableData[index].propor=this.tableData[index].num>0?((this.tableData[index].num/this.totle_num)*100).toFixed(0)+"%":'0%'
        }
      })
    },
    handleChange1(value){
      if(value>=this.input4){this.input4=value+1;}
    },
    handleChange2(value){
      if(this.input3>=value){this.input3=value-1;}
    },
  },
}
</script>




