<template>
  <div id="app" class="fsb ais" style="background-color: #f8f8f8;">
    <navbar class="nav" v-if="!(path ==='/login')"></navbar>
    <!-- <navbar class="nav" v-show="true"></navbar> -->
    <div class="rou fg2">
      <Header class="hea"  v-if="this.$route.path!='/login'"></Header>
      <router-view class="" style=""></router-view>
    </div>
  </div>
</template>

<script>
import { Cell, CellGroup, Icon, Button  } from 'vant';
import axios from 'axios';
import navbar from '@/components/navbar.vue';
import Header from '@/components/header.vue';
export default {
  components: {
    navbar,
    Header,
  },
  data() {
    return {
      path:'',
      token:sessionStorage.getItem('token'),
    }
  },
  // 判断路由
  updated() {
    var token=sessionStorage.getItem('token')
    if(!token){
      this.$router.push('/login')
    }
  },
  mounted() {
    this.path = this.$route.path;
  },
  watch:{
    $route(to,from){
        this.path = to.path
        if(this.path!=='/login'){
          document.documentElement.scrollTop =0
        }

    },
    // 'token':function (n,o) {
    //   console.log(n)
    // }
  }
}

</script>

<style>
body {
  /* background-color: #f8f8f8; */
}

.van-cell__title .van-icon {
  font-size: 18px;
}
.nav{
  width: auto;
  height: 100vh;
  z-index: 9;
}
.hea{
  display: block;
  left: 120px;
  width: 100%;
  background: #a6ced8;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  height: 62px;
}
.rou{
  margin-top: 60px;
}
</style>



