<template>
  <div id="orderData_box">
    <!-- header -->
    <div class="fsb mg-10 ">
       <EXport :ex_data='ex_data'></EXport>
      <div class="">
        <el-select v-model="value1" placeholder="" ><el-option
            label="全部收银员"
            value="null">
          </el-option>
          <el-option
            v-for="item in options1"
            :key="item.id"
            :label="item.realname"
            :value="item.id+''">
          </el-option>
        </el-select>
        <el-select v-model="value2" placeholder="" >
          <el-option
            v-for="item in options2"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <div class="dslb">
          <el-date-picker
            v-model="value_1"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </div>
        <el-input v-model="input" placeholder="订单编号" style="width:150px;"></el-input>
        <el-button type="warning" v-on:click="getdata">统计分析</el-button>
      </div>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
    <PageCoat @getpage='getpage' :total='total'></PageCoat>
  </div>
</template>

<script>
  import Table from '../../../components/table';
  import PageCoat from '../../../components/pagination';
  import EXport from '../../../components/export';
  export default {
    components:{
      Table,
      PageCoat,
      EXport,
    },
    data() {
      return {
        options1: [],
        options2: [
          {
            value: '0',
            label: '全部支付方式'
          }, {
            value: '21',
            label: '支付宝'
          }, {
            value: '11',
            label: '微信'
          }, {
            value: '31',
            label: '银联'
          },{
            value: '41',
            label: '现金支付'
          },
        ],
        tableData: [],
        table_title:['序号','订单编号','日期','收银员','实收金额','支付方式','商品数量'],
        proplist:['index','order_sn','create_time','user_name','practical_price','pay_type','total_num'],
        table_height:'600',
        value1: 'null',
        value2: '0',
        value_1: [this.$code.yestedate(),this.$code.todate()],
        isdate:false,
        input:'',
        total:0,
        onepage:10,
        pagenow:1,
        ex_data:{}
      }
    },
    watch: {
      'tableData':function (n,o) {
        this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'订单数据'}
      }
    },
    created() {
      this.getmemberlist()
    },
    mounted() {

    },
    updated() {

    },
    methods: {
      gain(){
        this.isdate=!this.isdate;
      },
      // 获取员工列表
      getmemberlist(){
        var data=false
        this.$api.getStaffList(data,res=>{
          // console.log(res.data[0].list)
          this.options1=res.data[0].list
          this.getdata()
        })
      },
      // 获取订单列表
      getdata(){
        let data={
          record_no:this.onepage,
          page_no:this.pagenow,
          start_date:this.value_1[0],
          end_date:this.value_1[1],
          order_sn:this.input,
          cashier_id:this.value1=='null'?'':this.value1,
          payment_way:this.value2=='0'?'':this.value2,
        }
        this.$api.getOrderList(data,res=>{
          this.tableData=res.data[0].list
          this.total=res.data[0].filter.total_number
          for(let index in this.tableData){
            this.tableData[index].index=index
            switch (this.tableData[index].pay_type) {
              case 0:
                this.tableData[index].pay_type='现金'
                break;
              case 1:
                this.tableData[index].pay_type='微信'
                break;
              case 2:
                this.tableData[index].pay_type='支付宝'
                break;
              case 3:
                this.tableData[index].pay_type='银联'
                break;
              default:
                break;
            }
          }
        })
      },
      getpage(data){
        switch (data.type) {
          case 0:
            this.onepage=data.val
            this.getdata()
            break;
          case 1:
            this.pagenow=data.val
            this.getdata()
            break;
          default:
            break;
        }
      }
    },
  }
</script>

<style lang="less" scoped>

</style>






