<template>
  <div id="staffRold">
    <!-- header -->
    <div class="txalE mg-10">
      <el-button type="danger" @click="del">删除</el-button>
      <el-button type="primary" @click="add('add')">新增</el-button>
    </div>
    <!-- table -->
    <el-table
        ref="multipleTable"
        :data="tableData"
        border
        tooltip-effect="dark"
        @selection-change="handleSelectionChange"
        style="width: 98%">
        <el-table-column
        type="selection"
        width="55">
        </el-table-column>
        <el-table-column
          label="角色">
         <template slot-scope="scope">{{ scope.row.rank==1?'雇员':'店主' }}</template>
        </el-table-column>
        <el-table-column label="是否启用" prop="radio">
          <template slot-scope="scope">
            <el-switch
             active-color="#13ce66"
             inactive-color="#ff4949"
             v-model="scope.row.is_ok"
             @change=switch_on(scope.$index,scope.row)>
             >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作"  >
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="handleEdit(scope.$index, scope.row,'edit')">编辑</el-button>
          </template>
        </el-table-column>
    </el-table>
    <PageCoat @getpage='getpage' :total='total'></PageCoat>
    <!-- add -->
    <TankCreate v-show="isTand" @changeShow='havechild' :butName='butName'></TankCreate>
  </div>
</template>

<script>
import TankCreate from './tankCreate'
import PageCoat from '../../../components/pagination'
export default {
  components:{
    TankCreate,PageCoat
  },
  data() {
    return {
      tableData1: [],
      isTand:false,
      butName:'',
      fromOne:{
        rold:'',
        allChecked:false
      },
      onepage:10,
      pagenow:1,
      total:0,
      is_ok:'',
      crera_ind:[],
    }
  },
  computed: {
    tableData:function () {
      return this.tableData1
    }
  },
  watch: {
    'fromOne.allChecked':function(n,o){
      console.log(n)
    },
  },
  created() {
    this.getdata()
  },
  updated() {
    // console.log(this.fromOne.allChecked)
  },
  methods: {
    switch_on(e,s){
      console.log(e)
      console.log(s)
      this.getdata()
    },
    getdata(){
      var data={
        page_no:this.pagenow,
        record_no:this.onepage,
        status:'',
        name:'',
      }
      this.$api.getStaffList(data,res=>{
        if(res.data.length==0){return false}
        this.total=Number(res.data[0].filter.total_number)
        this.tableData1=res.data[0].list
        for(let item of this.tableData1){
          if(item.is_active==0){
            item.is_ok=false
          }else{
            item.is_ok=true
          }
        }
      })
    },
    getpage(data){
      switch (data.type) {
        case 0:
          this.onepage=data.val
          this.getdata()
          break;
        case 1:
          this.pagenow=data.val
          this.getdata()
          break;
        default:
          break;
      }
    },
    handleEdit(index,row,type){
      this.isTand=!this.isTand;
      console.log(index,row,type)
      type=='edit'?this.butName='编辑-店主':'';
    },
    handleSelectionChange(val) {
        console.log(val)
        this.crera_ind=val
    },
    del(){
      if(this.crera_ind.length==0){
        this.$message('请先选择操作对象');
        return false
      }
      this.$message({
          type: 'info',
          message: '该角色下已绑定用户，请删除后进行此操作',
          duration:2000
        });
    },
    add(type){
      console.log(type)
      this.isTand=!this.isTand;
      type=='add'?this.butName='新增':'';
    },
    havechild(data){
      this.isTand=data.show
      console.log(data)
      if(data.type=='ok'){

      }
    }
  },
}
</script>

<style lang="less" scoped>
.tandL{
    width: 139px;
    padding: 5px 10px;
}
</style>




