<template>
  <div id="sale_box">
    <!-- header -->
    <div class="fsb mg-10 ">
      <EXport :ex_data='ex_data'></EXport>
      <div class="">
        <div class="dslb">
          <el-date-picker
            v-model="value_1"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </div>
        <el-input v-model="input_cound" placeholder="商品条码" style="width:150px;"></el-input>
        <el-input v-model="input_name" placeholder="名称" style="width:150px;"></el-input>
        <el-button type="warning" v-on:click="getdata">查询</el-button>
      </div>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :proplist='proplist' :table_title='table_title' :table_height='table_height'></Table>
    <!-- foot -->
    <div class="mg-10">
      <span>记录数：{{foot.total_order_no}} ，总销量：{{foot.total_goods_num}} ，总应收：{{foot.total_revenue}} ，总优惠：{{foot.total_discount}}，总实收：{{foot.total_customerpay}}</span>
    </div>
  </div>
</template>

<script>
  import Table from '../../../components/table';
  import EXport from '../../../components/export';
  export default {
    components:{
      Table,EXport
    },
    data() {
      return {
        input_cound:'',
        input_name:'',
        tableData: [],
        table_title:['序号','商品名称','商品条码','规格','单位','商品分类','现有库存','销售数量','商品总价','实收金额',
        '实收占比','利润','利润率','创建时间'],
        proplist:['index','goods_name','goods_sn','spec','unit','cat_name','repertory','order_number','salesand','revenue',
        'propor','profit','profitOne','create_time'],
        table_height:'600',
        value_1: [this.$code.yestedate(),this.$code.todate()],
        ex_data:{},
        foot:{}
      }
    },
    watch: {
      'tableData':function (n,o) {
        this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'销售统计'}
      }
    },
    created(){
      this.getdata()
    },
    updated(){

    },
    methods: {
      getdata(){
        let data={
          start_date:this.value_1[0],
          end_date:this.value_1[1],
          goods_name:this.input_name,
          goods_sn:this.input_cound,
          is_order_by_money:'',
          is_today: '',
          is_yesterday: '',
          is_week: '',
          is_month: '',
        }
        this.$api.getSalesreviewList(data,res=>{
          console.log(res)
          if(res.data.length==0){return false}
          this.foot=res.data[0].list
          this.tableData=res.data[0].list.table
          let total_revenue=res.data[0].list.total_revenue
          for(let index in this.tableData){
            this.tableData[index].index=index
            this.tableData[index].propor=(this.tableData[index].revenue/total_revenue*100).toFixed(2)+'%'
            this.tableData[index].profit=(this.tableData[index].revenue-this.tableData[index].cost_basic).toFixed(2)
            this.tableData[index].salesand=(this.tableData[index].sales*this.tableData[index].order_number).toFixed(2)
            this.tableData[index].profitOne=this.tableData[index].profit/this.tableData[index].revenue
            this.tableData[index].create_time=this.$code.date(new Date(Number(this.tableData[index].goods_create_time)),0)
          }
        })
      }
    },
  }
</script>




