<template>
  <div id="member_all_box">
    <!-- header -->
    <HeaderChaqnge @headerC='getheaderC1' @chart='chart1' :title='title1'></HeaderChaqnge>
    <!-- center one -->
    <div class="fsb padd-30">
      <div class="txal bg-gray one_box fdc jc-center">
        <div class="padd-tb-10">
          <h5 class="dslb">会员总数（个）：</h5>
          <h5 class="dslb blue">{{menberData.all_member_1}}</h5>
        </div>
        <div class="padd-tb-10">
          <h5 class="dslb">新增会员（个）：</h5>
          <h5 class="dslb blue">{{menberData.new_member_number_1}}</h5>
        </div>
      </div>
      <div class=" bg-gray one_box fsa aic">
        <div class="mg-rl-15 txal dslb">
          <h5 class="padd-tb-10 dslb">总交易金额（元）：</h5>
          <h5 class="padd-tb-10 blue dslb">{{(menberData.new_member_order_sum + menberData.old_member_order_sum) | toUSD2}}</h5>
        </div>
        <i class="dslb h100pc" style="border: 1px solid #d2cbcb;height: 130px;"></i>
        <div class="mg-rl-15 dslb">
          <div class="padd-tb-10">
            <h5 class="dslb ">新会员交易金额（元）：</h5>
            <h5 class="dslb blue">{{menberData.new_member_order_sum | toUSD2}}</h5>
          </div>
          <div class="padd-tb-10">
            <h5 class="dslb">老会员交易金额（元）：</h5>
            <h5 class="dslb blue">{{menberData.old_member_order_sum | toUSD2}}</h5>
          </div>
        </div>
      </div>
    </div>
    <!-- center two -->
    <div class="fsb padd-30">
      <div class=" bg-gray one_box fsa aic">
        <div class="mg-rl-15 txal dslb">
          <h5 class="padd-tb-10 dslb">交易订单（笔）：</h5>
          <h5 class="padd-tb-10 blue dslb">{{ menberData.new_member_order_number + menberData.old_member_order_number }}</h5>
        </div>
        <i class="dslb h100pc" style="border: 1px solid #d2cbcb;height: 130px;"></i>
        <div class="mg-rl-15 dslb">
          <div class="padd-tb-10">
            <h5 class="dslb ">新会员交易订单（笔）：</h5>
            <h5 class="dslb blue">{{menberData.new_member_order_number}}</h5>
          </div>
          <div class="padd-tb-10">
            <h5 class="dslb">老会员交易订单（笔）：</h5>
            <h5 class="dslb blue">{{menberData.old_member_order_number}}</h5>
          </div>
        </div>
      </div>
      <div class=" bg-gray one_box fsa aic">
        <div class="mg-rl-15 txal dslb">
          <h5 class="padd-tb-10 dslb">客单价（元）：</h5>
          <h5 class="padd-tb-10 blue dslb">{{all_kdj | toUSD2}}</h5>
        </div>
        <i class="dslb h100pc" style="border: 1px solid #d2cbcb;height: 130px;"></i>
        <div class="mg-rl-15 dslb">
          <div class="padd-tb-10">
            <h5 class="dslb ">新会员交易客单价（元）：</h5>
            <h5 class="dslb blue">{{all_kdj_1 | toUSD2}}</h5>
          </div>
          <div class="padd-tb-10">
            <h5 class="dslb">老会员交易客单价（元）：</h5>
            <h5 class="dslb blue">{{all_kdj_2 | toUSD2}}</h5>
          </div>
        </div>
      </div>
    </div>
    <!-- center three -->
    <div class="padd-25">
      <div class='txalS'>
        <i class="dslb h100pc" style="border: 2px solid #d2cbcb;height: 20px;"></i>
        <h5 class='dslb'>会员排名-按消费金额排名</h5>
      </div>
    </div>
    <!-- TABLE -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
    <!-- header fore -->
    <HeaderChaqnge @headerC='getheaderC2' @chart='chart2' :title='title2'></HeaderChaqnge>
    <!-- chart -->
    <div id="achart" style="height:300px;width:98%;"></div>
  </div>
</template>

<script>
import HeaderChaqnge from '../../components/headerChange.vue';
import Table from '../../components/table';
export default {
  components:{
    HeaderChaqnge,
    Table
  },
  data() {
    return {
      title1:'会员概览',
      title2:'数据分析',
      tableData: [],
      proplist:['index','uname','phone','total_consumption','propor'],
      table_title:['排名','会员名称','手机号','消费金额','占比'],
      table_height:'500',
      value_1:[],
      value_2:[],
      is_today:true,
      is_yesterday:false,
      is_week:false,
      is_month:false,
      menberData:{},
      bar_list_x:[],
      bar_list_y:[],
    }
  },
  computed: {
    all_kdj:function () {
      let n=(this.menberData.new_member_order_sum + this.menberData.old_member_order_sum)/(this.menberData.new_member_order_number + this.menberData.old_member_order_number);
      if(!n){n=0}
      return n
    },
    all_kdj_1:function () {
      let n1=this.menberData.new_member_order_sum/this.menberData.new_member_order_number;
      if(!n1){n1=0}
      return n1
    },
    all_kdj_2:function () {
      let n2=this.menberData.old_member_order_sum/this.menberData.old_member_order_number;
      if(!n2){n2=0}
      return n2
    },
  },
  created() {
    this.getmemberOutlook()
    this.getrankByComptionNo()
  },
  mounted() {

  },
  methods: {
    getmemberOutlook(){
      let data={
        start_date:this.value_1.length>0?this.value_1[0]:'',
        end_date:this.value_1.length>0?this.value_1[1]:'',
        is_today:this.is_today?1:2,
        is_yesterday:this.is_yesterday?1:2,
        is_week:this.is_week?1:2,
        is_month:this.is_month?1:2,
      }
      this.$api.memberOutlook(data,res=>{
        if(res.data.length==0){return false}
        this.menberData=res.data[0].list
        this.tableData=res.data[0].list.table
        for(let index in this.tableData){
          this.tableData[index].propor=Number(this.tableData[index].total_consumption)/Number(res.data[0].list.total_comption)?(Number(this.tableData[index].total_consumption)/Number(res.data[0].list.total_comption)*100).toFixed(0)+'%':0
        }
      })
    },
    getrankByComptionNo(){
      let data_rankByComptionNo={
        start_date:this.value_2.length>0?this.value_2[0]:'',
        end_date:this.value_2.length>0?this.value_2[1]:'',
        is_today:this.is_today?1:2,
        is_yesterday:this.is_yesterday?1:2,
        is_week:this.is_week?1:2,
        is_month:this.is_month?1:2,
      }
      this.$api.rankByComptionNo(data_rankByComptionNo,res=>{
        this.bar_list_x=[];this.bar_list_y=[]
        // console.log(res)
        if(res.data.length==0||res.data[0].list.length==0){
          return false
          }
        for(let item of res.data[0].list){
          this.bar_list_x.push(item.user_name)
          this.bar_list_y.push(item.total_num)
        }
        this.$chart.bar2('achart',this.bar_list_x,this.bar_list_y,['#ab5353']);
      })
    },
    getheaderC1(data){
      console.log(1,data)
      switch (data){
        case 0:
          this.is_today=true;
          this.is_yesterday=false;
          this.is_week=false;
          this.is_month=false;
          this.value_1=[]
          this.getmemberOutlook()
          break;
        case 1:
          this.is_yesterday=true;
          this.is_today=false;
          this.is_week=false;
          this.is_month=false;
          this.value_1=[]
          this.getmemberOutlook()
          break;
        case 2:
          this.is_week=true;
          this.is_yesterday=false;
          this.is_today=false;
          this.is_month=false;
          this.value_1=[]
          this.getmemberOutlook()
          break;
        case 3:
          this.is_month=true;
          this.is_week=false;
          this.is_yesterday=false;
          this.is_today=false;
          this.value_1=[]
          this.getmemberOutlook()
          break;
        case 4:
          this.is_today=false;
          this.is_yesterday=false;
          this.is_week=false;
          this.is_month=false;
          this.value_1=[]
          break;
        default:
          break;
      }
    },
    chart1(data){
      console.log(1,data)
      this.value_1=data.value1
      this.getmemberOutlook()
    },
    getheaderC2(data){
      console.log(2,data)
      switch (data){
        case 0:
          this.is_today=true;
          this.is_yesterday=false;
          this.is_week=false;
          this.is_month=false;
          this.value_2=[]
          this.getrankByComptionNo()
          break;
        case 1:
          this.is_yesterday=true;
          this.is_today=false;
          this.is_week=false;
          this.is_month=false;
          this.value_2=[]
          this.getrankByComptionNo()
          break;
        case 2:
          this.is_week=true;
          this.is_yesterday=false;
          this.is_today=false;
          this.is_month=false;
          this.value_2=[]
          this.getrankByComptionNo()
          break;
        case 3:
          this.is_month=true;
          this.is_week=false;
          this.is_yesterday=false;
          this.is_today=false;
          this.value_2=[]
          this.getrankByComptionNo()
          break;
        case 4:
          this.is_today=false;
          this.is_yesterday=false;
          this.is_week=false;
          this.is_month=false;
          this.value_2=[]
          break;
        default:
          break;
      }
    },
    chart2(data){
      console.log(2,data)
      this.value_2=data.value1
      this.getrankByComptionNo()
    },
  },
}
</script>

<style lang="less" scoped>
.one_box{
  width:36%;
  height: 130px;
}
</style>



