<template>
  <div id="shopping_cen_data_box">
    <!-- header -->
    <div class="fsb mg-10 ">
      <div>
        <el-button type="primary" v-on:click="S_add">新增</el-button>
        <el-button type="primary" v-on:click="S_import">导入</el-button>
        <EXport :ex_data='ex_data' class="dslb"></EXport>
      </div>
      <div class="">
        <el-select v-model="value" placeholder="" >
          <el-option
            label="全部分类"
            value="1000">
          </el-option>
          <el-option
            v-for="item in options1"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
        <el-input v-model="barcode" placeholder="商品条码" style="width:150px;"></el-input>
        <el-input v-model="goods_name" placeholder="商品名称" style="width:150px;"></el-input>
        <el-button type="warning" v-on:click="getdata">查询</el-button>
      </div>
    </div>
      <!-- table -->
      <el-table
        :data="tableData"
        border
        style="width: 99%">
        <el-table-column
          prop="index"
          label="序号"
          width="">
        </el-table-column>
        <el-table-column label="操作" align='center'>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="goods_name"
          label="商品名称"
          width="">
        </el-table-column>
        <el-table-column
          prop="goods_sn"
          label="商品编码">
        </el-table-column>
        <el-table-column
          prop="cat_id"
          label="分类">
        </el-table-column>
        <el-table-column
          prop="shop_price"
          label="零售价格">
        </el-table-column>
        <el-table-column
          prop="cost_price"
          label="成本价">
        </el-table-column>
        <el-table-column
          prop="type"
          label="计价方式">
        </el-table-column>
        <el-table-column
          prop="repertory"
          label="初始库存">
        </el-table-column>
        <el-table-column
          prop="repertory_caution"
          label="库存预警">
        </el-table-column>
        <el-table-column
          prop="spec"
          label="规格型号">
        </el-table-column>
        <el-table-column
          prop="unit"
          label="商品单位">
        </el-table-column>
        <el-table-column
          prop="place_code"
          label="货架号">
        </el-table-column>
        <el-table-column
          prop="staleTime"
          label="过期日期">
        </el-table-column>
        <el-table-column
          prop="is_forsale"
          label="是否上架">
        </el-table-column>
        <el-table-column
          prop="is_short"
          label="是否快捷">
        </el-table-column>
        <el-table-column
          prop="create_time"
          label="创建日期">
        </el-table-column>
      </el-table>
      <PageCoat @getpage='getpage' :total='total'></PageCoat>
      <!-- import -->
      <ImportS v-show="show_import" @changeShow='haveChild'></ImportS>
      <!-- add -->
      <shopCenDa v-show="show_add" @changeShow='haveChildAdd' :type='type' :isedit='isedit' :editform='editform' :options1='options1'></shopCenDa>
  </div>
</template>

<script>
import ImportS from '@/components/import.vue';
import shopCenDa from '@/components/shopCenDa.vue';
import PageCoat from '../../../components/pagination';
import EXport from '../../../components/export';
  export default {
    components:{
      ImportS,
      shopCenDa,
      PageCoat,
      EXport,
		},
    data() {
      return {
        barcode:'',
        goods_name:'',
        file_val:'选择文件',
        options1: [],
        tableData: [],
        table_title:['序号','商品名称','商品编码','分类','零售价格','成本价','计价方式','初始库存','库存预警','规格型号','商品单位','货架号','过期日期','是否上架','是否快捷','创建日期'],
        proplist:['index','goods_name','goods_sn','cat_id','shop_price','cost_price','type','repertory','repertory_caution','spec','unit','place_code','staleTime','is_forsale','is_short','create_time'],
        value:'1000',
        value_1: '',
        value_2: '',
        show_import:false,
        show_add:false,
        formInline: {
          class: '',
          onemoney: '',
          zeromoney:'',
          meth:'',
          beginstore:'',
          endstore:'',
          big:'',
          num:'',
          enddate:'',
          ispush:'',
          isfrist:'',
        },
        editform:{},
        imageUrl: '',
        shopping_code:'',
        shop_name:'',
        imgfile:'',
        isedit:false,
        type:'',
        total:0,
        onepage:10,
        pagenow:1,
        ex_data:{}
      }
    },
    watch: {
      'tableData':function (n,o) {
        this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'商品资料'}
      }
    },
    created() {
      this.getclass()
    },
    updated() {
      console.log();
    },
    methods: {
      getclass(){
        let data1={}
        this.$api.getcategorylist(data1,res=>{
          this.options1=res.data[0]
          this.getdata()
        })
      },
      getdata(){
        let data={
          record_no:this.onepage,
          page_no:this.pagenow,
          barcode:this.barcode,
          goods_name:this.goods_name,
          cat_id:this.value=='1000'?'':this.value,
          type:'',
          is_forsale:'',
        }
        this.$api.getGoodsList(data,res=>{
          if(res.data.length==0){return false}
          this.total=res.data[0].filter.total_number
          this.tableData=res.data[0].list
          for(let index in this.tableData){
            this.tableData[index].index=Number(index)+1;
            this.tableData[index].type=this.tableData[index].type==0?'计件':'计重';
            this.tableData[index].is_forsale=this.tableData[index].is_forsale==0?'否':'是';
            this.tableData[index].is_short=this.tableData[index].is_short==0?'否':'是';
            this.tableData[index].staleTime=this.$code.date(new Date(Number(this.tableData[index].staleTime)),1)
            this.tableData[index].cat_id=this.$code.classFor(this.options1,this.tableData[index].cat_id);
          }
        })
      },
      getpage(data){
        switch (data.type) {
          case 0:
            this.onepage=data.val
            this.getdata()
            break;
          case 1:
            this.pagenow=data.val
            this.getdata()
            break;
          default:
            break;
        }
      },

      handleEdit(index, row){
        this.dele_id=row.local_id
        this.show_add=true
        this.isedit=true
        this.type='edit'
        this.editform=row
      },
      S_import(){
        this.show_import=!this.show_import
      },
      S_add(s){
        this.show_add=true
        this.isedit=false;
        this.type='add'
      },

      onSubmit() {
        console.log('submit!');
      },
      resetForm(formName) {

      },
      haveChild(data){
        this.show_import=data
      },
      haveChildAdd(data){
        // console.log(data);
        this.show_add=data.is;
        if(data.type=='ok'){this.getdata()}
        if(data.type=='no_eui'){
          this.editform={}
        }
        if(data.type=='dele'){
          let dale_data={goods_local_id:this.dele_id}
          this.$api.delgoods(dale_data,res=>{
            this.getdata()
          })
        }
      },
    },
  }
</script>


<style lang="less" scoped>
.lh{
  line-height:90px;
}
.mu_one{
  background: #999;
  width: 100px;
  line-height: 30px;
}
.mu_two{
  background: #fff;
   width: 300px;
  line-height: 30px;
  color:blue;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>




