import Vue from 'vue';
import App from './App.vue';

import {Tooltip,Pagination,Dialog,Loading,InputNumber, Button,MessageBox,Cascader,Col,Autocomplete,Switch,Message ,Radio,CheckboxGroup,RadioGroup,RadioButton, Select,Upload, Option, Row, Form, FormItem,Checkbox, Input, Tree, DatePicker, Table,
  TableColumn, } from 'element-ui';
Vue.use(Tooltip);
Vue.use(Dialog);
Vue.use(Pagination);
Vue.use(Button);
Vue.use(Select);
Vue.use(Option);
Vue.use(Row);
Vue.use(Col);
Vue.use(Form);
Vue.use(Switch);
Vue.use(FormItem);
Vue.use(Checkbox);
Vue.use(Input);
Vue.use(Tree);
Vue.use(DatePicker);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Cascader);
Vue.use(Upload);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(RadioButton);
Vue.use(CheckboxGroup );
Vue.use(Autocomplete );
Vue.use(InputNumber);
Vue.use(Loading.directive);
Vue.prototype.$loading = Loading.service;
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;
Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$prompt = MessageBox.prompt;
Vue.prototype.$message = Message;
import router from './router';
import store from './store';
import md5 from 'js-md5';
import axios from 'axios';
import VueAxios from 'vue-axios';
import myCharts from './common/myCharts.js'
import ex from './common/ex.js'
import http from './common/https.js'
import api from './common/api.js'
import code from './common/code.js'
import '@/style/globle-px.css';
import '@/style/reset.css';

axios.interceptors.request.use(function (config) {
  let len=JSON.stringify(config.data).match(/(<|>)/g)
  if(len){Message('请勿输入特殊字符');return false;}
  Loading.service({
    lock: true,
    text: 'Loading',
    spinner: 'el-icon-loading',
    background: 'rgba(0, 0, 0, 0.7)'
  })
  return config
})
axios.interceptors.response.use(function (res) {
  Loading.service().close()
  // console.log(res)
  Message(res.data.message)
  if(res.data.code==0){return false}
  return res
},function(err){
  // console.log(err)
  // console.log(err.response.status)
  Loading.service().close()
  Message('数据接收失败')
  return err
})

Vue.filter('toUSD', function (value) { 　return value?value.toFixed(0):value})
Vue.filter('toUSD2', function (value) { 　return value?value.toFixed(2):value})
Vue.filter('date', function (date) { 　
  var y=date.getFullYear();
  var M=date.getMonth()+1;
  var d=date.getDate();
  var hd=y+'-'+M+'-'+d
  return hd
})
Vue.use(myCharts)
Vue.use(ex)
Vue.use(VueAxios,axios);
Vue.prototype.$md5 = md5;
Vue.prototype.$https = http;
Vue.prototype.$code = code;
Vue.prototype.$api = api;
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
});



