<template>
  <div id="shop_Cen_box">
    <div class="fixed" style="z-index: 1;top:0;left:0;width:100vw;height:100vh;"></div>
     <!-- add -->
      <div class="fixed bg-gray overauto" style="z-index: 2;top:10%;left:40%;width:700px;height:690px;">
        <div class="fsb">
          <UpLoad @getUpload='getUpload' :imageUrls='formInline.goods_picture'></UpLoad>
          <div class="fg2 bg-fff">
            <div class="fsb lh mg-rl-20">
              <p class="dslb">商品编码:</p>
              <el-input class="dslb" style="width:200px;" :disabled="isedit?true:false" v-model="formInline.goods_sn"></el-input><el-button v-show="!isedit" type="warning" @click="getbar_code">生成条码</el-button>
              <!-- <p class="dslb">{{formInline.goods_sn}}<el-button v-show="!isedit" type="warning">生成条码</el-button></p> -->
            </div>
            <div class="fsb lh mg-rl-20">
              <p class="dslb">商品名称:</p>
              <el-input v-model="formInline.goods_name" placeholder="" style="width:72%;"></el-input>
            </div>
          </div>
        </div>
        <h5 class="gray mg-20">商品属性信息录入</h5>
        <el-form :inline="true" :model="formInline" class="demo-form-inline mg-10">
          <el-form-item label="商品分类">
            <!-- <el-input v-model="formInline.cat_id" placeholder=""></el-input> -->
            <el-select v-model="formInline.cat_id" placeholder="" >
              <el-option
                v-for="item in options1"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="零售价格">
            <el-input v-model="formInline.shop_price" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="成本价格">
            <el-input v-model="formInline.cost_price" :disabled="isedit?true:false" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="计价方式">
            <!-- <el-input v-model="formInline.type" placeholder=""></el-input> -->
            <el-radio v-model="formInline.type" border label="1">计重</el-radio>
            <el-radio v-model="formInline.type" border label="0">计件</el-radio>
          </el-form-item>
          <el-form-item label="初始库存">
            <el-input v-model="formInline.repertory" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="库存预警">
            <el-input v-model="formInline.repertory_caution" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="规格型号">
            <el-input v-model="formInline.spec" :disabled="isedit?true:false" placeholder="640ml"></el-input>
          </el-form-item>
          <el-form-item label="商品单位">
            <el-input v-model="formInline.unit" placeholder="瓶"></el-input>
          </el-form-item>
          <el-form-item label="货 架  号 ">
            <el-input class="mg-l-3" v-model="formInline.place_code" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="过期日期">
            <!-- <el-input v-model="formInline.staleTime" placeholder=""></el-input> -->
            <el-date-picker
              v-model="formInline.staleTime"
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="是否上架">
            <!-- <el-input v-model="formInline.ispush" placeholder=""></el-input> -->
            <el-radio-group v-model="formInline.is_forsale" size="medium">
              <el-radio border label="1">是</el-radio>
              <el-radio border label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="是否快捷" class=" mg-l-20">
            <!-- <el-input v-model="formInline.isfrist" placeholder=""></el-input> -->

              <el-radio v-model="formInline.is_short" border label="1">是</el-radio>
              <el-radio v-model="formInline.is_short" border label="0">否</el-radio>

          </el-form-item>
          <el-form-item class="txalE" style="display:block;">
            <div >
              <el-button v-show="isedit" @click="dele('dele')">删除</el-button>
              <div class="mg-l-100 dslb">
                <el-button @click="adddele(isedit?'':'add')">取消</el-button>
                <el-button type="primary" @click="submitForm(formInline)">保存</el-button>
              </div>
            </div>
          </el-form-item>
        </el-form>
      </div>
  </div>
</template>

<script>
import UpLoad from './upload'
export default {
  props:["isedit","type",'editform','options1'],
  components:{
    UpLoad,
  },
  data() {
    return {
      imageUrl: '',
      to_data:{
        token:sessionStorage.getItem('token')?sessionStorage.getItem('token'):''
      },
      formInline: {
        goods_name:'',
        goods_sn:'',
        cat_id: '',
        goods_picture:'',
        type:'0',
        spec:'',
        unit:'',
        cost_price:'',
        shop_price:'',
        repertory:'',
        repertory_caution:'',
        place_code:'',
        is_forsale:'1',
        is_short:'0'
      },
      resetformInline: {
        goods_name:'',
        goods_sn:'',
        cat_id: 1,
        goods_picture:'',
        type:'0',
        spec:'',
        unit:'',
        cost_price:'',
        shop_price:'',
        repertory:'',
        repertory_caution:'',
        place_code:'',
        is_forsale:'1',
        is_short:'0'
      },
      file:{},
    }
  },
  watch: {
    'isedit':function (n,o) {
      if(n){}else{this.formInline=this.$code.copyobj(this.resetformInline)}
    },
    'type':function (n,o) {
      if(n=='add'){
        this.formInline=this.$code.copyobj(this.resetformInline)
      }
    },
    'options1':function (n,o) {
      this.formInline.cat_id=n[0].id
    },
    'editform':function (n,o) {
      let a=this.$code.copyobj(n)
      a.is_forsale=='否'?a.is_forsale='0':a.is_forsale='1'
      a.is_short=='否'?a.is_short='0':a.is_short='1'
      a.goods_import_id_list=a.id
      this.imageUrl=a.goods_picture
      this.formInline=a
      this.formInline.type=='计件'?this.formInline.type='0':this.formInline.type='1'
    }
  },
  methods: {
    getbar_code(){
      let bar_data={}
      this.$api.getbarcode(bar_data,res=>{
        console.log(res)
        this.formInline.goods_sn=res.data[0].barcode
      })
    },
    getUpload(data){
      console.log(data)
      this.formInline.goods_picture=data
    },
    dele(s){
      this.$confirm('确定删除该商品?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$emit('changeShow',{is:false,type:'dele'})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            duration:1000
          });
        });
    },
    adddele(s){
      if(s=='add'){
        this.$confirm('确定取消新增?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$emit('changeShow',{is:false,type:'no'})
        }).catch(() => {this.$emit('changeShow',{is:true,type:'no'})});
      }else{
        this.$emit('changeShow',{is:false,type:'no_eui'})
      }
    },
    submitForm(form){
      console.log(form)
      console.log(typeof(form['unit']))
      let isok=false
      for(let i in form){
        i=='place_code'||i=='staleTime'?isok=false:isok=true
        if(isok){
          if(!form[i]){this.$message('请完整填写商品信息');return false;}
        }
      }
      console.log(this.type)
      form.staleTime=new Date(form.staleTime+'').valueOf()
      if(this.type=='add'){
        this.$api.createGoods(form,res=>{
          console.log(res)
          if(res.code==1){this.$message("创建成功");this.$emit('changeShow',{is:false,type:'ok'});this.formInline=this.resetformInline;this.imageUrl='';}else{this.$message("创建失败")}
        })
      }else{
        // form.goods_import_id_list=
        this.$api.updateGoods(form,res=>{
          if(res.code==0){this.$message('更改失败');return false};
          this.$message("更改成功");
          this.$emit('changeShow',{is:false,type:'ok'});this.formInline=this.resetformInline;this.imageUrl=''
        })
      }
    },
  },
}
</script>

<style lang="less" scoped>
.mu_one{
  background: #999;
  width: 100px;
  line-height: 30px;
}
.mu_two{
  background: #fff;
   width: 300px;
  line-height: 30px;
  color:blue;
}
.lh{
  line-height:90px;
}
</style>





