<template>
  <div id="export_box">
    <el-button type="primary" @click="ex">导出</el-button>
  </div>
</template>

<script>
export default {
  props:['ex_data'],
  methods: {
    open(msg) {
      this.$message(msg);
    },
    ex(){
      if(!this.ex_data.tableData){this.open('表单无数据'); return false}
      if(this.ex_data.tableData.length>0){
        this.$excel.excel(this.ex_data)
      }else{
        this.open('表单无数据')
      }
    }
  },
}
</script>




