<template>
    <div id='center_cre'>
        <!-- header -->
        <div class="txalE mg-10 ">
            <!-- <el-button type="primary">导出</el-button> -->
            <el-select v-model="value1" placeholder="" >
                <el-option
                  label="全部分类"
                  value="1000">
                </el-option>
                <el-option
                  v-for="item in options1"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
            </el-select>
            <el-select v-model="value2" placeholder="" >
                <el-option
                v-for="item in options2"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="value3" placeholder="" >
                <el-option
                v-for="item in options3"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-input v-model="barcode" placeholder="商品条码" style="width:150px;"></el-input>
            <el-input v-model="goods_name" placeholder="商品名称" style="width:150px;"></el-input>
            <el-button type="warning" v-on:click="getdata">查询</el-button>
        </div>
        <!-- table -->
        <el-table
            ref="multipleTable"
            :data="tableData"
            height="550"
            tooltip-effect="dark"
            style="width: 98%"
            @selection-change="handleSelectionChange">
            <el-table-column
            type="selection"
            width="55">
            </el-table-column>
            <el-table-column v-for="(item,index) in table_title" :key='index'
              :prop="proplist[index]"
              :label="item"
              :sortable='proplist[index]==""?true:false'
              width="">
            </el-table-column>
        </el-table>
        <PageCoat @getpage='getpage' :total='total'></PageCoat>
        <!-- foot -->
        <div class="mg-bt-10">
          <p class="dslb">已选中（<span class="blue">{{check_num}}</span>）</p>
          <p class="dslb">全部<span class="blue">（{{all_num}}</span>）</p>
          <el-button type="danger" v-on:click='morecreate'>批量设置</el-button>
        </div>
        <!-- morecreate -->
        <div v-show='ismorecreate' class="z-index-2 fixed bg-gray bor_le_66" style="top:9%;left:29%;width:50%;">
          <h5 class="lh-58 bor_le_66">批量设置（<span class="blue">{{check_num}}</span>个商品）</h5>
          <div class="fsb">
            <el-tree
              :data="createData"
              node-key='index'
              :current-node-key='current_key'
              :highlight-current='true'
              style="position:relative;width:10%;height:100%;"
              :props="defaultProps"
              ref='treeBox'
              @node-click="handleNodeClick">
            </el-tree>
            <div class="rel" style="width:90%;height:549px;">
              <h3 class="mg-30" v-show='tree_index==0'>将选中商品设置分类：</h3>
              <h3 class="mg-30" v-show='tree_index==1'>将选中商品设置零售价：</h3>
              <h3 class="mg-30" v-show='tree_index==2'>将选中商品设置成本价：</h3>
              <h3 class="mg-30" v-show='tree_index==3'>将选中商品设置计价方式：</h3>
              <h3 class="mg-30" v-show='tree_index==4'>将选中商品设置库存预警：</h3>
              <h3 class="mg-30" v-show='tree_index==5'>将选中商品设置规格型号：</h3>
              <h3 class="mg-30" v-show='tree_index==6'>将选中商品设置商品单位：</h3>
              <h3 class="mg-30" v-show='tree_index==7'>将选中商品设置货架号：</h3>
              <h3 class="mg-30" v-show='tree_index==8'>将选中商品设置过期日期：</h3>
              <h3 class="mg-30" v-show='tree_index==9'>将选中商品设置是否上架：</h3>
              <h3 class="mg-30" v-show='tree_index==10'>将选中商品设置是否快捷：</h3>
              <el-form class="mg-l-30" ref="form" :model="form" label-width="80px">
                <el-form-item label="">
                  <el-radio-group v-model="form.cat_id" v-show='tree_index==0'>
                    <el-radio v-for="(item,index) in opti" :key='index' :label="item.id" class="mg-30">{{item.name}}</el-radio>
                  </el-radio-group>
                  <h5 class="dslb" v-show='tree_index==1'>零售价格：</h5><el-input v-model="form.shop_price" v-show='tree_index==1'></el-input>
                  <h5 class="dslb" v-show='tree_index==2'>成本价格：</h5><el-input v-model="form.cost_price" v-show='tree_index==2'></el-input>
                  <el-radio-group v-model="form.type" v-show='tree_index==3'>
                    <el-radio  label="0" class="mg-30">计件</el-radio>
                    <el-radio  label="1" class="mg-30">计重</el-radio>
                  </el-radio-group>
                  <h5 class="dslb" v-show='tree_index==4'>库存预警：</h5><el-input v-model="form.repertory_caution" v-show='tree_index==4'></el-input>
                  <h5 class="dslb" v-show='tree_index==5'>规格型号：</h5><el-input v-model="form.spec" v-show='tree_index==5'></el-input>
                  <h5 class="dslb" v-show='tree_index==6'>商品单位：</h5><el-input v-model="form.unit" v-show='tree_index==6'></el-input>
                  <h5 class="dslb" v-show='tree_index==7'>货架号：</h5><el-input v-model="form.place_code" v-show='tree_index==7'></el-input>
                  <el-date-picker
                    v-show='tree_index==8'
                    v-model="form.staleTime"
                    type="date"
                    placeholder="选择日期">
                  </el-date-picker>
                  <h5 class="dslb" v-show='tree_index==9'>是否上架：</h5>
                  <el-switch
                    v-model="form.is_forsale"
                    v-show='tree_index==9'
                    active-color="#13ce66"
                    inactive-color="#ff4949">
                  </el-switch>
                  <h5 class="dslb" v-show='tree_index==10'>是否快捷：</h5>
                  <el-switch
                    v-show='tree_index==10'
                    v-model="form.is_short"
                    active-color="#13ce66"
                    inactive-color="#ff4949">
                  </el-switch>
                </el-form-item>
                <el-form-item style="position: absolute;text-align: end;bottom: 0;right: 100px;">
                  <el-button @click="nomorecreate">取消</el-button>
                  <el-button type="primary" @click="onSubmit(form)">立即创建</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import PageCoat from '../../../components/pagination';
  export default {
    components:{
      PageCoat,
    },
    data() {
      return {
        options1: [],
        options2: [
          {
            value: '1000',
            label: '全部计价方式'
          }, {
            value: '0',
            label: '计件'
          },{
            value: '1',
            label: '计重'
          }
        ],
        options3: [
          {
            value: '1',
            label: '上架'
          }, {
            value: '0',
            label: '未上架'
          }
        ],
        current_key:'0',
        table_title:['序号','商品名称','商品编码','分类','零售价格','成本价','计价方式','初始库存','库存预警','规格型号','商品单位','货架号','过期日期','是否上架','是否快捷'],
        proplist:['index','goods_name','goods_sn','cat_id','shop_price','cost_price','type','repertory','repertory_caution','spec','unit','place_code','staleTime','is_forsale','is_short'],
        barcode:'',
        goods_name:'',
        value1: '1000',
        value2: '1000',
        value3: '1',
        tableData: [],
        multipleSelection: [],
        check_num:0,
        all_num:0,
        ismorecreate:false,
        tree_index:0,
        createData:[{
            label: '商品分类',
            index:0
          }, {
            label: '零售价格',
            index:1
            }, {
              label: '成本价格',
              index:2
          }, {
            label: '计价方式',
            index:3
          }, {
              label: '库存预警',
              index:4
          }, {
              label: '规格型号',
              index:5
          }, {
              label: '商品单位',
              index:6
          }, {
              label: '货架号',
              index:7
          }, {
              label: '过期日期',
              index:8
          }, {
              label: '是否上架',
              index:9
          }, {
              label: '是否快捷',
              index:10
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        form:{
          cat_id:'',
          shop_price:'',
          goods_name:'',
          goods_sn:'',
          goods_picture:'',
          cost_price:'',
          type:'',
          repertory_caution:'',
          spec:'',
          unit:'',
          place_code:'',
          staleTime:'',
          is_forsale:'',
          is_short:'',
          goods_import_id_list:''
        },
        reset_form:{
          cat_id:'',
          shop_price:'',
          goods_name:'',
          goods_sn:'',
          goods_picture:'',
          cost_price:'',
          type:'',
          repertory_caution:'',
          spec:'',
          unit:'',
          place_code:'',
          staleTime:'',
          is_forsale:'',
          is_short:'',
          goods_import_id_list:''
        },
        total:0,
        onepage:10,
        pagenow:1,
      }
    },
    computed: {
      opti:function(){
        return this.options1
      }
    },
    beforeCreate() {

    },
    created() {
      this.getclass()
    },
    updated() {
      this.all_num=this.tableData.length
    },
    methods: {
      getclass(){
        let data1={}
        this.$api.getcategorylist(data1,res=>{
          this.options1=res.data[0]
          console.log(this.options1)
          this.getdata()
        })
      },
      getpage(data){
        switch (data.type) {
          case 0:
            this.onepage=data.val
            this.getdata()
            break;
          case 1:
            this.pagenow=data.val
            this.getdata()
            break;
          default:
            break;
        }
      },
      getdata(){
        let data={
          record_no:this.onepage,
          page_no:this.pagenow,
          cat_id:this.value1==1000?'':this.value1,
          barcode:this.barcode,
          goods_name:this.goods_name,
          type:this.value2==1000?'':this.value2,
          is_forsale:this.value3,
        }
        this.$api.getGoodsList(data,res=>{
          console.log(res)
          if(res.data.length==0){return false}
          this.total=res.data[0].filter.total_number
          this.tableData=res.data[0].list
          for(let index in this.tableData){
            this.tableData[index].index=Number(index)+1;
            this.tableData[index].type=this.tableData[index].type==0?'计件':'计重';
            this.tableData[index].is_forsale=this.tableData[index].is_forsale==0?'否':'是';
            this.tableData[index].is_short=this.tableData[index].is_short==0?'否':'是';
            this.tableData[index].staleTime=this.$code.date(new Date(Number(this.tableData[index].staleTime)),1)
            this.tableData[index].cat_id=this.$code.classFor(this.options1,this.tableData[index].cat_id);
          }
          console.log(this.tableData)
        })
      },
      onSubmit(from) {
        from.is_forsale==false?from.is_forsale=0:from.is_forsale=1
        from.is_short==false?from.is_short=0:from.is_short=1
        console.log(from);
        this.$api.updateGoods(from,res=>{
          if(res.code==0){this.$message('更改失败');return false};
          this.ismorecreate=false;
          this.$message('更改成功');
          this.getdata();
        })
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        var id_arr=[]
        var name_arr=[]
        var sn_arr=[]
        this.form.goods_import_id_list
        this.multipleSelection = val;
        console.log(val)
        console.log(val.length)
        for(let item of val){
          id_arr.push(item.id)
          name_arr.push(item.goods_name)
          sn_arr.push(item.goods_sn)
        }
        this.form.goods_import_id_list=id_arr.toString()
        this.form.goods_name=name_arr.toString()
        this.form.goods_sn=sn_arr.toString()
        this.check_num=val.length
      },
      morecreate(){
        console.log(this.tree_index)
        if(this.check_num>0){this.ismorecreate=true;}else{this.$message('请选择商品')}
        this.$refs.treeBox.setCurrentKey(0);
      },
      nomorecreate(){
        this.ismorecreate=false;
      },
      handleNodeClick(data) {
        console.log(data);
        this.tree_index=data.index
      },
    }
  }
</script>



