<template>
  <div :class="[is_ss?'smo_width':'big_width']">
    <div id="nav_box" class="rel fsb ais fixed h100pc">
      <div class="fdc aic one_box">
        <img class="bg_star padd-10" src="../../static/reset/登录/bg_star.png" alt="" srcset="">
        <div
          class="two_box txal lh-15 fdc aic mg-l-6 padd-tb-8"
          :class="[item.index==per_ind?'bg-fff':'']"
          v-for="(item,index) in data"
          :key='index'
          @click="per(item)">
          <img :src="item.index==per_ind?item.re_url:item.url" alt="" srcset="" style="width:20px;height:20px;">
          <span :class="item.index==per_ind?item.re_font_color:item.font_color" class="f14">{{item.label}}</span>
        </div>
      </div>
      <div class="fdc aic bg-fff h100pc" style="width:136px;" v-show="is_ss">
        <div class="fsb aic padd-16 bor_b_gray_0 bob box_two_or">
          <img :src="item_ch.re_url" alt="" srcset="" style="width:18px;height:18px;">
          <h1 class="f17 fg2 dslb mg-l-6">{{item_ch.label}}</h1>
          <img :src="item_ch.re_url" alt="" @click="is_s" srcset="" style="width:10px;height:10px;">
        </div>
        <div class="bor_b_gray_0 padd-tb-20" v-for="(it,index) in item_ch.children" :key="index">
          <div v-if='it.children' class=''>
            <p class=" f14">{{it.label}}</p>
            <div v-for="(ii,inde) in it.children" :key="inde" class="mg-l-10 f13 gray-666 padd-tb-10" style="width:65px;">
              <router-link :to="ii.router" class="f13" :class="[ii.index==i_ch?'blue_0':'a_0']" @click.native="childaa(ii)" tag="span">{{ii.label}}</router-link>
            </div>
          </div>
          <router-link v-else :to="it.router" class="f13" :class="[it.index==i_ch?'blue_0':'a_0']" @click.native="childaa(it)" tag="span">{{it.label}}</router-link>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
export default {
  data() {
    return {
      data: [
        {
          label: '概览',
          index:'0',
          router:'/',
          url:require('../../static/reset/菜单切图/icon_overview_normat.png'),
          re_url:require('../../static/reset/菜单切图/icon_overview_pressed.png'),
          font_color:'white',
          re_font_color:'blue_0',
        },
        {
          label: '销售',
          index:'1',
          url:require('../../static/reset/菜单切图/icon_sales_normat.png'),
          re_url:require('../../static/reset/菜单切图/icon_sales_pressed.png'),
          font_color:'white',
          re_font_color:'blue_0',
          children: [
            {
              label: '营业概况',
              index:'1_0',
              // router:'/sale/view',
              router:'#',
            },
            {
              label: '数据分析',
              // index:'1_1',
              children: [
                {
                  label: '营业趋势分析',
                  index:'1_1_0',
                  router:'/sale/data/trend',
                },
                {
                  label: '营业占比分析',
                  index:'1_1_1',
                  router:'/sale/data/proportion',
                },
                {
                  label: '商品销售统计',
                  index:'1_1_2',
                  router:'/sale/data/sale',
                },
                {
                  label: '笔单价分析',
                  index:'1_1_3',
                  router:'/sale/data/one',
                },
              ]
            },
            {
              label: '订单中心',
              // index:'1_2',
              children: [
                {
                  label: '订单数据',
                  index:'1_2_0',
                  router:'/sale/order/data',
                },
              ]
            },
            {
              label: '交班记录',
              // index:'1_3',
              children: [
                {
                  label: '日结记录',
                  index:'1_3_0',
                  // router:'/sale/record/day'
                  router:'#'
                },
                {
                  label: '交班记录',
                  index:'1_3_1',
                  router:'/sale/record/class'
                },
              ]
            },
          ],
        },
        {
          label: '商品',
          index:'2',
          url:require('../../static/reset/菜单切图/icon_goods_normat.png'),
          re_url:require('../../static/reset/菜单切图/icon_goods_pressed.png'),
          font_color:'white',
          re_font_color:'blue_0',
          children: [
            {
              label: '商品概览',
              index:'2_0',
              router:'/shopping/overview'
            },
            {
              label: '商品中心',
              children: [
                {
                  label: '商品资料',
                  index:'2_1_0',
                  router:'/shopping/center/data'
                },
                {
                  label: '商品分类',
                  index:'2_1_1',
                  // router:'/shopping/center/class'
                  router:'#'
                },
                {
                  label: '商品设置',
                  index:'2_1_2',
                  router:'/shopping/center/create'
                },
              ]
            },
            {
              label: '库存',
              children: [
                {
                  label: '库存查询',
                  index:'2_2_0',
                  router:'/shopping/store/chart'
                },
                {
                  label: '库存预警',
                  index:'2_2_1',
                  // router:'/shopping/store/del'
                  router:'#'
                },
                {
                  label: '变动明细',
                  index:'2_2_2',
                  // router:'/shopping/store/change'
                  router:'#'
                },
                {
                  label: '盘点历史',
                  index:'2_2_3',
                  // router:'/shopping/store/history'
                  router:'#'
                },
              ]
            },
          ],
        },
        {
          label: '员工',
          index:'3',
          url:require('../../static/reset/菜单切图/icon_staff_normat.png'),
          re_url:require('../../static/reset/菜单切图/icon_staff_pressed.png'),
          font_color:'white',
          re_font_color:'blue_0',
          children: [
            {
              label: '员工中心',
              children:[
                {
                  label: '员工管理',
                  index:'3_0_0',
                  router:'/staff/staffCen/create'
                },
                {
                  label: '收银员业绩',
                  index:'3_0_1',
                  router:'/staff/staffCen/succ'
                },
              ]
            },
          ],
        },
        {
          label: '会员',
          index:'4',
          url:require('../../static/reset/菜单切图/icon_vip_normat.png'),
          re_url:require('../../static/reset/菜单切图/icon_vip_pressed-t.png'),
          font_color:'white',
          re_font_color:'blue_0',
          children: [
            {
              label: '会员概览',
              index:'4_0',
              router:'/member/allview'
            },
            {
              label: '会员中心',
              children:[
                {
                  label:'会员管理',
                  index:'4_1_0',
                  router:'/member/center/create'
                }
              ]
            },
            {
              label: '统计报表',
              children:[
                {
                  label:'会员消费信息分析',
                  index:'4_2_0',
                  router:'/member/table/massge'
                },
                {
                  label:'客单价分析',
                  index:'4_2_1',
                  router:'/member/table/once'
                },
                {
                  label:'积分分布',
                  index:'4_2_2',
                  router:'/member/table/scor'
                },
              ]
            },
          ],
        },
        {
          label: '数据',
          index:'5',
          router:'/data',
          url:require('../../static/reset/菜单切图/icon_data_normat.png'),
          re_url:require('../../static/reset/菜单切图/icon_data_pressed.png'),
          font_color:'white',
          re_font_color:'blue_0',
        },
        {
          label: '设置',
          index:'6',
          url:require('../../static/reset/菜单切图/icon_set_normat.png'),
          re_url:require('../../static/reset/菜单切图/icon_set_pressed.png'),
          font_color:'white',
          re_font_color:'blue_0',
          children: [
            {
              label: '店铺信息管理',
              index:'6_0',
              router:'/create/data'
            },
            {
              label: '系统设置',
              index:'6_1',
              router:'/create/sysCra'
            },
            {
              label: '操作日志',
              index:'6_2',
              // router:'/create/useDate'
              router:'#'
            }
          ],
        }
      ],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      curr_key:'',
      curr_arr:[],
      per_ind:'0',
      item_ch:{},
      i_ch:{},
      is_ss:false
    }
  },
  computed: {
    is_show(){
      // if(item_ch.children){this.is_ss=true}else{this.is_ss=false}
      // return this.is_ss
    }
  },
  watch: {
    $route(n,o){
      // console.log(n)
      if(n.hash=='#'){this.$message('待开发')}
    },
  },
  created(){
    if(sessionStorage.getItem('per_ind')){
      this.per_ind=sessionStorage.getItem('per_ind')
      this.item_ch=JSON.parse(sessionStorage.getItem('item_ch'))
      this.i_ch=sessionStorage.getItem('i_ch')
    }
  },
  updated() {

  },
  methods: {
    is_s(){
      this.is_ss=false
    },
    handleNodeClick(data) {
      // console.log(this.$refs.tree.getCurrentKey())
      switch (data.index) {
        case '0':
          this.$router.push('/')
          break;
        case '1':
          // this.$router.push('/sale/view_0')
          break;
        case '1_0':
          // this.$router.push('/sale/view')

          break;
        case '1_1_0':
          this.$router.push('/sale/data/trend')
          break;
        case '1_1_1':
          this.$router.push('/sale/data/proportion')
          break;
        case '1_1_2':
          this.$router.push('/sale/data/sale')
          break;
        case '1_1_3':
          this.$router.push('/sale/data/one')
          break;
        case '1_2_0':
          this.$router.push('/sale/order/data')
          break;
        case '1_3_0':
          // this.$router.push('/sale/record/day')
          break;
        case '1_3_1':
          this.$router.push('/sale/record/class')
          break;
        case '2_0':
          this.$router.push('/shopping/overview')
          break;
        case '2_1_0':
          this.$router.push('/shopping/center/data')
          break;
        case '2_1_1':
          // this.$router.push('/shopping/center/class')
          break;
        case '2_1_2':
          this.$router.push('/shopping/center/create')
          break;
        case '2_2_0':
          this.$router.push('/shopping/store/chart')
          break;
        case '2_2_1':
          // this.$router.push('/shopping/store/del')
          break;
        case '2_2_2':
          // this.$router.push('/shopping/store/change')
          break;
        case '2_2_3':
          // this.$router.push('/shopping/store/history')
          break;
        case '3_0_0':
          this.$router.push('/staff/staffCen/create')
          break;
        case '3_0_1':
          this.$router.push('/staff/staffCen/succ')
          break;
        case '4_0':
          this.$router.push('/member/allview')
          break;
        case '4_1_0':
          this.$router.push('/member/center/create')
          break;
        case '4_2_0':
          this.$router.push('/member/table/massge')
          break;
        case '4_2_1':
          this.$router.push('/member/table/once')
          break;
        case '4_2_2':
          this.$router.push('/member/table/scor')
          break;
        case '5':
          this.$router.push('/data')
          break;
        case '6_0':
          this.$router.push('/create/data')
          break;
        case '6_1':
          this.$router.push('/create/sysCra')
          break;
        case '6_2':
          // this.$router.push('/create/useDate')
          break;

        default:
          break;
      }
    },
    per(item){
      console.log(item)
      this.per_ind=item.index
      this.item_ch=item
      sessionStorage.setItem("per_ind", item.index);
      sessionStorage.setItem("item_ch", JSON.stringify(item));
      if(item.children){
        this.is_ss=true
      }else{
        this.is_ss=false
        this.$router.push(item.router)
        sessionStorage.setItem("i_ch",'');
        this.$store.commit('updateTitle',item.label)
      }
    },
    childaa(i){
      console.log(i)
      this.i_ch=i.index
      sessionStorage.setItem("i_ch", i.index);
      this.$store.commit('updateTitle',i.label)
    }
  }
}
</script>

<style lang="less" scoped>
.big_width{
  width: 85px;
}
.smo_width{
  width:221px;
}
.a_0{
  color: #666666;
}
.one_box{
  width: 85px;
  height: 100%;
  background: #0066FF;
  .bg_star{
    width: 70px;
    height: 42px;
  }
  .two_box{
    width: 78px
  }
}
.router-link-active {
    text-decoration: none;
}
.box_two_or{
  width: 122px;
}
.el-tree--highlight-current .el-tree-node.is-current>.el-tree-node__content {
  background: #a8a8dc;
}
.el-tree{
  height: 100vh;
  background: #f8f8f8;
  position: fixed;
  top: 0px;
  .el-tree-node{
    .el-tree-node__content{
      height: 50px;
      .el-tree-node__label{
        padding-right: 22px;
      }
    }

  }
  .el-tree-node:focus>.el-tree-node__content{
    background-color: #a8a8dc;
  }
}

</style>






