<template>
  <div id="import_box">
     <!-- import -->
    <div  class="fixed bg-gray bor-14" style="z-index: 1;top:20%;left:40%;width:500px;">
      <div class='fsb mg-20'>
        <p><i class='dslb bg-skyblue' style='width:5px;height:16px;'></i><span class='gray'>批量导入</span></p>
        <p class="f24 gray" v-on:click='S_import'>X</p>
      </div>
      <div class="txal mg-20">
        <div class="">
          <!-- <p class='dslb mu_one'>模板</p>
          <p class='dslb mu_two' @click="downloadPDf">下载模板</p> -->
          <el-button type="primary" @click="downloadPDf">下载模板</el-button>
        </div>
        <!-- <div class="mg-10 rel">
          <p class='dslb mu_one'>选择文件</p>
          <p onclick="getElementById('file').click()" class='dslb mu_two' style="cursor:pointer;">{{file_val}}</p>
          <input v-show='false' type="file" multiple="multiple" id="file" name='file' value='file_val' class='dslb mu_two'/>
        </div> -->
        <div class="mg-10 rel">
          <el-upload
            class="upload-demo"
            ref="upload"
            action="http://owner.123.com/api/index/importgoodsdata"
            :on-success='handsecces'
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :file-list="fileList"
            :data='imp_data'
            :auto-upload="false">
            <el-button slot="trigger" size="" type="primary">选取文件</el-button>
          </el-upload>
        </div>
        <p class="red txalS mg-20">请按照模板填写商品信息，已存在商品会被自动覆盖</p>
      </div>
      <div class="fsa mg-20">
        <el-button v-on:click='S_import'>取消</el-button>
        <el-button type="primary" @click="submitUpload">上传</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      file_val:'选择文件',
      fileList:[],
      imp_data:{token:sessionStorage.getItem('token')?sessionStorage.getItem('token'):''}
    }
  },
  created() {
    console.log(this.$store.state.root_url)
  },
  updated() {

  },

  methods: {
    S_import(){
        this.$emit('changeShow',false)
    },
    downloadPDf() {
      window.open(this.$store.state.root_url+'/static/excel/template.xlsx');
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handsecces(res, file, fileList){
      if(res.code==1){
        this.$message('上传成功')
        this.$emit('changeShow',false)
        this.fileList=[]
      }
    }
  },
}
</script>

<style lang="less" scoped>
.mu_one{
  background: #999;
  width: 100px;
  line-height: 30px;
}
.mu_two{
  background: #fff;
   width: 300px;
  line-height: 30px;
  color:blue;
}
</style>



