<template>
  <div id="pagination_box">
    <div class="txalE">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 50]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    props:['total'],
    methods: {
      handleSizeChange(val) {
        this.$emit('getpage',{type:0,val})
      },
      handleCurrentChange(val) {
        this.$emit('getpage',{type:1,val})
      },
    },
    data() {
      return {
        currentPage: 1
      };
    }
  }
</script>



