<template>
  <div id='header_box'>
    <div class="fsb aic mg-0 jc-end f17 bg-fff">
      <h1 class="texa">{{header_tils}}</h1>
      <div class="fsb mg-r-20">
        <img class="img_0 circle" src="../../static/header_img/header_1.png" alt="">
        <span class="dslb padd-rl-10 text_0">星星便利店</span>
        <img class="dslb mg-l-10 img_1" @click="clear" src="../../static/header_img/header_2.png" alt="">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      header_til:'概览'
    }
  },
  computed: {
    header_tils(){
      return this.$store.getters.title
    }
  },
  watch: {

  },
  created() {
    // this.header_til=this.$store.getters.title
  },
  updated() {
    console.log(this.$store)
    if(sessionStorage.getItem('header_til')){
      this.header_til=sessionStorage.getItem('header_til')
    }
  },
  methods: {
    clear(){
      var data={}
      this.$api.logout(data,res=>{
        sessionStorage.clear();
        this.$store.commit('updateTitle','概览')
        this.$router.push('/login')
      })
    }
  },
}
</script>

<style lang="less" scoped>
.img_0{
  width: 33px;
  height: 33px;
}
.img_1{
  width: 17px;
  height: 16px;
}
.text_0{
  border-right: 1px solid #666666;
}
.texa{
  margin-left: 105px;
  height: 62px;
  line-height: 62px;
}
</style>




