<template>
<div id="memberCre_box">
  <div class="fixed" style="z-index: 2;top:0;left:0;width:100vw;height:100vh;"></div>
  <div class="fixed bg-gray overauto" style="z-index: 3;top:10%;left:40%;width:700px;height:560px;">
    <div class="fsb">
      <UpLoad @getUpload='getUpload' :imageUrls='imageUrl'></UpLoad>
      <div class="fg2 bg-fff">
        <div class="fsb lh mg-rl-20">
          <p class="dslb">姓名</p>
          <el-input class='w78' v-model="formInline.name" placeholder="" ></el-input>
        </div>
        <div class="fsb lh mg-rl-20">
          <p class="dslb">手机号</p>
          <el-input class='w78' v-model="formInline.ponle" placeholder="" ></el-input>
        </div>
        <div class="fsb lh mg-rl-20">
          <p class="dslb">证件号</p>
          <el-input class='w78' v-model="formInline.codeid" placeholder="" ></el-input>
        </div>
      </div>
    </div>
    <h5 class="gray mg-20">会员信息录入</h5>
    <div class="fsb lh mg-rl-20">
      <p class="dslb">性别</p>
      <!-- <el-input class='w78' v-model="formInline.sex" placeholder="" ></el-input> -->
      <el-radio v-model="formInline.sex" border label="1">女</el-radio>
      <el-radio v-model="formInline.sex" border label="0">男</el-radio>
    </div>
    <div class="fsb lh mg-rl-20">
      <p class="dslb">生日</p>
      <!-- <el-input class='w78' v-model="formInline.bir" placeholder="" ></el-input> -->
      <el-date-picker
        v-model="formInline.bir"
        type="date"
        placeholder="选择日期">
      </el-date-picker>
    </div>
    <div class="fsb lh mg-rl-20">
      <p class="dslb">会员折扣</p>
      <el-input class='w78' v-model="formInline.member" placeholder="" ></el-input>
    </div>
    <div class="fsb lh mg-rl-20">
      <p class="dslb">备注</p>
      <el-input class='w78' type="textarea" v-model="formInline.msg"  placeholder=""></el-input>
    </div>
    <div class="fsb lh mg-rl-20">
      <el-button v-show="isedit" @click="dele('dele')">删除</el-button>
      <div class="mg-l-100 dslb">
        <el-button @click="adddele(isedit?'':'add')">取消</el-button>
        <el-button type="primary" @click="submitForm(formInline,type)">保存</el-button>
      </div>
    </div>
  </div>
</div>

</template>

<script>
import UpLoad from './upload'
export default {
  props:["isedit","type",'member_id','member_data'],
  components:{
    UpLoad,
  },
  data() {
    return {
      // isedit:false,
      formInline: {
        id:'',
        name:'',
        ponle:'',
        codeid:'',
        sex:'0',
        bir:'',
        member:'100',
        msg:'',
        goods_picture:''
      },
      reformInline: {
        id:'',
        name:'',
        ponle:'',
        codeid:'',
        sex:'0',
        bir:'',
        member:'100',
        msg:'',
        goods_picture:''
      },
      imageUrl: '',
      shopname:'',
    }
  },
  mounted() {

  },
  updated() {

  },
  computed:{

  },
  watch: {
    'type':function (n,o) {
      console.log(n)
      if(n=='add'){this.formInline=this.$code.copyobj(this.reformInline);}
    },
    'isedit':function(n,o){
      // console.log(n)
    },
    'member_id':function (n,o) {
      // this.formInline.id=n
    },
    member_data:function (n,o) {
      this.formInline = {
        id:n.id,
        name:n.uname,
        ponle:n.phone,
        codeid:n.idcard,
        sex:n.gender=='男'?'0':'1',
        bir:this.$code.date(new Date(n.birthday),0),
        member:n.discount,
        msg:n.comment,
        goods_picture:n.pic
      },
      this.imageUrl=n.pic
      console.log(this.formInline)
    }
  },
  methods: {
    handleAvatarSuccess(res, file) {
      console.log(res)
      console.log(file)
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      console.log(file)
      this.imgfile=file
    },
    adddele(type){
      this.$emit('changeShow',{is:false,type:'no'})
    },
    dele(type){
      if(type!='dele'){return false}
      let dele_data={
        mem_id:this.member_id
      }
      this.$api.delmem(dele_data,res=>{
        this.$emit('changeShow',{is:false,type:'ok'})
      })
    },
    submitForm(form,type){
      console.log(form)
      console.log(type)
      if(type=='edit'){
        let data={
          id:form.id,
          uname:form.name,
          phone:form.ponle,
          idcard:form.codeid,
          pic:form.goods_picture,
          gender:form.sex,
          discount:form.member,
          comment:form.msg,
          birthday:form.bir.length<12?(new Date(form.bir)).valueOf():(form.bir).valueOf(),
          // birthday:1365465465467,
        }
        this.$api.updateMember(data,res=>{
          if(res.code==0){return false}
          this.$emit('changeShow',{is:false,type:'ok'})
        })
      }

    },
    getUpload(data){
      console.log(data)
      this.formInline.goods_picture=data
    },
  },
}
</script>


<style lang="less" scoped>
.w78{
  width: 89%;
}
.mu_one{
  background: #999;
  width: 100px;
  line-height: 30px;
}
.mu_two{
  background: #fff;
   width: 300px;
  line-height: 30px;
  color:blue;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.lh{
  line-height:60px;
}
</style>



