<template>
  <div id="staffSucc">
    <!-- header -->
    <div class="fsb mg-10 ">
      <EXport :ex_data='ex_data' class=""></EXport>
      <div class="">
        <el-select v-model="value" placeholder="" >
          <el-option
            label="全部"
            value="1000">
          </el-option>
          <el-option
           v-for="item in options"
            :key="item.id"
            :label="item.realname"
            :value="item.id+''">
          </el-option>
        </el-select>
        <div class="dslb">
          <el-date-picker
          v-model="value_1"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
        </div>
        <el-button type="warning" v-on:click="getdata">统计分析</el-button>
      </div>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
    <PageCoat @getpage='getpage' :total='total'></PageCoat>
  </div>
</template>

<script>
  import Table from '../../../components/table';
  import PageCoat from '../../../components/pagination';
  import EXport from '../../../components/export';
  export default {
    components:{
      Table,PageCoat,EXport
    },
    data() {
      return {
        options: [],
        tableData: [],
        table_title:['开始时间','结束时间','收银员','收银总额','现金支付','银联支付','支付宝支付','微信支付'],
        proplist:['start_time','end_time','realname','total_price','xj','yl','zfb','wx'],
        table_height:'500',
        value: '1000',
        value_1: [this.$code.yestedate(),this.$code.todate()],
        total:0,
        onepage:10,
        pagenow:1,
        ex_data:{},
      }
    },
    watch: {
      'tableData':function (n,o) {
        this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'收银员业绩'}
      }
    },
    created() {
      this.getmemberlist()
    },
    mounted(){

    },
    updated() {

    },
    methods: {
      // 获取员工列表
      getmemberlist(){
        var data=false
        this.$api.getStaffList(data,res=>{
          // console.log(res.data[0].list)
          this.options=res.data[0].list
          this.getdata()
        })
      },
      getdata(){
        let data={
          record_no:this.onepage,
          page_no:this.pagenow,
          start_date:this.value_1[0],
          end_date:this.value_1[1],
          staff_id:this.value=='1000'?'':this.value
        }
        this.$api.getClassList(data,res=>{
          this.total=res.data[0].filter.total_number
          this.tableData=res.data[0].list
          for(let index in this.tableData){
            this.tableData[index].start_time=this.$code.date(new Date(Number(this.tableData[index].start_time)),1)
            this.tableData[index].end_time=this.$code.date(new Date(Number(this.tableData[index].end_time)),1)
          }
        })
      },
      getpage(data){
        switch (data.type) {
          case 0:
            this.onepage=data.val
            this.getdata()
            break;
          case 1:
            this.pagenow=data.val
            this.getdata()
            break;
          default:
            break;
        }
      },
    },
  }
</script>



