<template>
  <div id="member_scor_box">
    <!-- header 1 -->
    <div class="mg-10">
       <h5 class="dslb">积分区间：</h5>
       <!-- <input v-model="input3" placeholder="" style="width:100px;"> -->
       <el-input-number v-model="input3" controls-position="right" @change="handleChange1" :min="0" :max="99999999"></el-input-number>
        <button>元</button>
        ——
        <!-- <input v-model="input4" placeholder="" style="width:100px;"> -->
        <el-input-number v-model="input4" controls-position="right" @change="handleChange2" :min="0" :max="99999999"></el-input-number>
        <button>元</button>
        <h6 class="dslb">均分</h6>
        <!-- <input v-model="input5" placeholder="" type="number" min='0' max="10" style="width:100px;"> -->
        <el-input-number v-model="input5" controls-position="right" :min="1" :max="10"></el-input-number>
        <button>段</button>
        <span>最多选取10段</span>
    </div>
    <!-- header 2 -->
    <div class="txalE mg-15">
      <h5 class="dslb">趋势图</h5>
      <el-button type="warning" @click="getdata">查询</el-button>
    </div>
    <!-- chart -->
    <div id="scorchart" style="height:300px;width:98%;"></div>
    <div class="fsb mg-15">
      <h5>数据详情</h5>
      <EXport :ex_data='ex_data' class="dslb"></EXport>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
  </div>
</template>

<script>
import Table from '../../../components/table';
import EXport from '../../../components/export';
export default {
  components:{
    Table,EXport
  },
  data() {
    return {
      input3:0,
      input4:1,
      input5:1,
      tableData: [],
      table_title:['序号','积分区间（分）','会员个数（个）'],
      proplist:['index','range','count'],
      table_height:'',
      ex_data:{},
    }
  },
  watch: {
    'tableData':function (n,o) {
      this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'积分分布'}
    }
  },
  created(){
    this.getdata()
  },
  mounted() {

  },
  methods: {
    getdata(){
      let data={
        min:this.input3,
        max:this.input4,
        num:this.input5,
      }
      this.$api.pointdis(data,res=>{
        console.log(res)
        this.tableData=res.data[0].list
        this.bar_list_x=[];this.bar_list_y=[]
        for(let item of this.tableData){
          this.bar_list_x.push(item.range)
          this.bar_list_y.push(item.count)
        }
        this.$chart.bar2('scorchart',this.bar_list_x,this.bar_list_y,['#bdb175']);
      })
    },
    handleChange1(value){
      if(value>=this.input4){this.input4=value+1;}
    },
    handleChange2(value){
      if(this.input3>=value){this.input3=value-1;}
    },
  },
}
</script>



