<template>
  <div id="one_box">
    <!-- header1 -->
    <div class="bg-gray">
      <h6 class="mg-15 dslb">笔单价：（笔单价是每一笔交易记录对应的平均交易金额，笔单价分析用于统计门店一段时间内交易金额的占比）</h6>
    </div>
    <!-- header2 -->
    <div class="mg-15">
      <h6 class="dslb">日期范围：</h6>
      <el-button  @click="chosedate(1)" :style="{background:is_num==1?'skyblue':'',color:is_num==1?'white':''}">本周</el-button>
      <el-button  @click="chosedate(2)" :style="{background:is_num==2?'skyblue':'',color:is_num==2?'white':''}">上周</el-button>
      <el-button  @click="chosedate(3)" :style="{background:is_num==3?'skyblue':'',color:is_num==3?'white':''}">本月</el-button>
      <el-button  @click="chosedate(4)" :style="{background:is_num==4?'skyblue':'',color:is_num==4?'white':''}">上月</el-button>
      <el-button  v-on:click='chosedate(5)'>自定义</el-button>
      <div v-show="isdate" class="dslb">
        <el-date-picker
          v-model="value1"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
      </div>
    </div>
    <!-- header3 -->
    <div class="mg-15">
       <h5 class="dslb">笔单区间：</h5>
       <!-- <input v-model="input3" placeholder="" style="width:100px;"> -->
        <el-input-number v-model="input3" controls-position="right" @change="handleChange1" :min="0" :max="99999999"></el-input-number>
        <button>元</button>
        ——
        <!-- <input v-model="input4" placeholder="" style="width:100px;"> -->
        <el-input-number v-model="input4" controls-position="right" @change="handleChange2" :min="0" :max="99999999"></el-input-number>
        <button>元</button>
        <h6 class="dslb">均分</h6>
        <!-- <input v-model="input5" placeholder="" type="number" min='0' max="10" style="width:100px;"> -->
        <el-input-number v-model="input5" controls-position="right" :min="1" :max="10"></el-input-number>
        <button>段</button>
        <span>最多选取10段</span>
    </div>
    <!-- content -->
    <div class="txalE mg-15">
      <EXport :ex_data='ex_data' class="dslb"></EXport>
      <el-button type="warning" @click='getdata'>查询</el-button>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
  </div>
</template>

<script>
import Table from '../../../components/table';
import EXport from '../../../components/export';
export default {
  components:{
    Table,EXport
  },
  data() {
    return {
      isdate:false,
      value1:'',
      input3:0,
      input4:1,
      input5:1,
      tableData: [],
      table_title:['序号','笔单价（元）','笔数','占比'],
      proplist:['index','range','count','propor'],
      table_height:'500',
      is_num:1,
      ex_data:'',
    }
  },
  watch: {
    'tableData':function (n,o) {
      this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'笔单价分析'}
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    chosedate(num){
      switch (num) {
        case 1:
          this.is_num=num
          this.isdate=false
          this.value1=''
          break;
        case 2:
          this.is_num=num
          this.isdate=false
          this.value1=''
          break;
        case 3:
          this.is_num=num
          this.isdate=false
          this.value1=''
          break;
        case 4:
          this.is_num=num
          this.isdate=false
          this.value1=''
          break;
        case 5:
          this.is_num=num
          this.isdate=true
          this.value1=''
          break;

        default:
          break;
      }
    },
    getdata(){
      let data={
        min:this.input3,
        max:this.input4,
        num:this.input5,
        is_current_week:this.is_num==1?1:2,
        is_previous_week:this.is_num==2?1:2,
        is_current_month:this.is_num==3?1:2,
        is_previous_month:this.is_num==4?1:2,
        start_date:this.value1?this.value1[0]:'',
        end_date:this.value1?this.value1[1]:'',
      }
      this.$api.perorderanalysis(data,res=>{
        console.log(res)
        this.tableData=res.data[0].list.data_list
        let toble_=res.data[0].list.total
        for(let index in this.tableData){
          this.tableData[index].propor=this.tableData[index].count>0?(this.tableData[index].count/toble_*100).toFixed(0)+'%':'0%'
        }
      })
    },
    handleChange1(value){
      if(value>=this.input4){this.input4=value+1;}
    },
    handleChange2(value){
      if(this.input3>=value){this.input3=value-1;}
    },
  },
}
</script>




