import axios from 'axios';
import Qs from 'qs';

axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
axios.defaults.transformRequest = [obj => Qs.stringify(obj)]

let Development='http://owner.123.com'
let production='http://tbpos.shangding365.com'
let urlroot=production
// let urlroot=production
const doajax = (url, data, type) => {

  var overurl = urlroot +'/api'+ url
  var token=sessionStorage.getItem('token')?sessionStorage.getItem('token'):false
  if(url!='/login/login'){
    data.token=token;
  }
  let config;
  if (type === 'get') {
      config = {
          method: type,
          params: data,
      }
    return axios(overurl, config);
  } else if(type=='post') {
      return axios.post(overurl,data)
  }
}

const ajaxget = (url, data, suc) => {
  doajax (url, data, 'get').then((res,s,n) => {
      let {
          status,
          msg
      } = res.data;
      res=res.data
      suc(res);
  });
}
const ajaxpost = (url, data, suc) => {
  doajax (url, data, 'post').then((res) => {
    res=res.data
    suc(res);
  });
}

export default {
  ajaxpost,
  ajaxget,
  urlroot
}



