<template>
  <div id="member_center_crea_box">
    <!-- header -->
    <div class="fsb mg-10 ">
      <div>
        <el-button type="primary" v-on:click="add">新增</el-button>
        <el-button type="primary" v-on:click="imports">导入</el-button>
        <EXport :ex_data='ex_data' class="dslb"></EXport>
      </div>
      <div class="">
        <el-input v-model="name" placeholder="姓名" style="width:150px;"></el-input>
        <el-input v-model="phone" placeholder="手机号" style="width:150px;"></el-input>
        <el-button type="warning" v-on:click="getdata">查询</el-button>
      </div>
    </div>
    <!-- import -->
    <ImportS v-show="show_import" @changeShow='haveChild'></ImportS>
    <!-- add/eidt -->
    <Membercre v-show="show_add" @changeShow='haveChildAdd' :member_id='member_id' :member_data='member_data' :type='type' :isedit='isedit'></Membercre>
    <!-- table -->
    <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="id"
        label="序号"
        width="">
      </el-table-column>
      <el-table-column label="操作" align='center'>
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="uname"
        label="姓名"
        width="">
      </el-table-column>
      <el-table-column
        prop="phone"
        label="手机号">
      </el-table-column>
      <el-table-column
        prop="idcard"
        label="证件号">
      </el-table-column>
      <el-table-column
        prop="points"
        label="可用积分">
      </el-table-column>
      <el-table-column
        prop="gender"
        label="性别">
      </el-table-column>
      <el-table-column
        prop="birthday"
        label="生日">
      </el-table-column>
      <el-table-column
        prop="create_time"
        label="成为会员时间">
      </el-table-column>
      <el-table-column
        prop="discount"
        label="折扣">
      </el-table-column>
      <el-table-column
        prop="total_consumption"
        label="累计消费">
      </el-table-column>
      <el-table-column
        prop="total_order"
        label="累计订单">
      </el-table-column>
      <el-table-column
        prop="comment"
        label="备注">
      </el-table-column>
    </el-table>
    <PageCoat @getpage='getpage' :total='total'></PageCoat>
  </div>
</template>

<script>
import ImportS from '@/components/import.vue';
import Membercre from '@/components/memberCre.vue';
import PageCoat from '../../../components/pagination';
import EXport from '../../../components/export';
export default {
  components:{
    ImportS,Membercre,PageCoat,EXport
  },
  data() {
    return {
      show_import:false,
      show_add:false,
      tableData: [],
      table_title:['序号','姓名','手机号','证件号','可用积分','性别','生日','成为会员时间','折扣','累计消费','累计订单','备注'],
      proplist:['id','uname','phone','idcard','points','gender','birthday','create_time','discount','total_consumption','total_order','comment'],
      isedit:false,
      type:'',
      name:'',
      phone:'',
      total:0,
      onepage:10,
      pagenow:1,
      ex_data:{},
      member_id:'',
      member_data:{},
    }
  },
  watch: {
    'tableData':function (n,o) {
      this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'会员管理'}
    }
  },
  created() {
    this.getdata()
    // console.log(this.$store.state.root_url)
  },
  updated() {

  },
  methods: {
    // 查询
    getdata(){
      let data={
        record_no:this.onepage,
        page_no:this.pagenow,
        name:this.name,
        mobile:this.phone
      }
      this.$api.getMemberList(data,res=>{
        console.log(res)
        if(res.data.length==0){return false}
        this.total=res.data[0].filter.total_number
        this.tableData=res.data[0].list
        for(let index in this.tableData){
          this.tableData[index].gender=this.tableData[index].gender==0?'男':'女'
          this.tableData[index].birthday=this.$code.date(new Date(Number(this.tableData[index].birthday)),0)
          this.tableData[index].create_time=this.$code.date(new Date(Number(this.tableData[index].create_time)),0)
        }
      })
    },
    // 导入
    imports(){
      this.show_import=true;
    },
    haveChild(data){
      // console.log(data);
      this.show_import=data;
    },
    haveChildAdd(data){
      // console.log(data);
      this.show_add=data.is;
      data.type=='ok'?this.getdata():this.member_data={}

    },
    // 新增
    add(data){
      this.show_add=true;
      this.isedit=false;
      this.type='add';

    },
    // 编辑
    handleEdit(index, row) {
      console.log(index, row);
      this.show_add=!this.show_add;
      this.isedit=true;
      this.type='edit';
      this.member_id=row.id
      this.member_data=row
    },
    getpage(data){
      switch (data.type) {
        case 0:
          this.onepage=data.val
          this.getdata()
          break;
        case 1:
          this.pagenow=data.val
          this.getdata()
          break;
        default:
          break;
      }
    },
  },
}
</script>




