import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from "vuex-persistedstate"
import Export from './module/export';
import https from '../common/https'

Vue.use(Vuex)

//存数据
const state={
  title:'',//默认初始值
  url:'/look/ing.img',
  flag:true,
  root_url:https.urlroot,
}

// 取数据
const getters={
  title:state=>state.title,
  url:state=>state.url,
  flag:state=>state.flag,
  flag:state=>state.flag,
}

// 改数据
const mutations={
  updateTitle:(state,payload)=>{
    state.title=payload;
  },
  updateUrl:(state,payload)=>{
    state.url=payload;
  },
  updateFlag:(state,payload)=>{
    state.flag=payload;
  },
}

// 提交
const actions={
  updateTitle:({commit},payload)=>{
    commit('updateTitle',payload);
  },
  updateUrl:({commit},payload)=>{
    state.url=payload;
  },
  updateFlag:({commit},payload)=>{
    state.one=payload;
  },
}

const store=new Vuex.Store({
  plugins: [createPersistedState({
    storage: window.sessionStorage,
  })],
  state,
  getters,
  mutations,
  actions,
  modules:{
    Export
  }
})

export default store;



