<template>
  <div id="proportion_box">
    <!-- header -->
    <div class="fsb mg-10 ">
      <EXport :ex_data='ex_data'></EXport>
      <div class="">
        <el-select v-model="value" placeholder="" >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <div class="dslb">
          <el-date-picker
            v-model="value_1"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </div>
        <el-button type="warning" v-on:click="getdata">统计分析</el-button>
      </div>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
  </div>
</template>

<script>
  import Table from '../../../components/table';
  import EXport from '../../../components/export';
  export default {
    components:{
      Table,EXport
    },
    data() {
      return {
        options: [
          {
            value: '1',
            label: '按商品分类'
          }, {
            value: '2',
            label: '按支付方式'
          }, {
            value: '4',
            label: '按是否会员'
          }, {
            value: '3',
            label: '按收银员'
          }
        ],
        tableData: [],
        table_title:['序号','商品分类','销售单数','商品数量','商品原价','销售额','营业占比','利润','笔单价'],
        proplist:['index','name','order_number','goods_number','sales','revenue','propor','profit','onecount'],
        table_height:'600',
        value: '1',
        value_1: [this.$code.yestedate(),this.$code.todate()],
        ex_data:{}
      }
    },
    created() {
      this.getdata()
    },
    watch: {
      'tableData':function (n,o) {
        this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'营业占比'}
      }
    },
    updated() {

    },
    methods: {
      getdata(){
        let sta_da=''
        sta_da=this.value_1[0]
        let data={
          start_date:sta_da,
          end_date:this.value_1[1],
          type:this.value,
          is_order_by_money:'',
          is_today: '',
          is_yesterday: '',
          is_week: '',
          is_month: '',
        }
        this.$api.getSalescomposeanalysisList(data,res=>{
          this.tableData=res.data[0].list.table
          let total_revenue=res.data[0].list.total_revenue
          for(let index in this.tableData){
            this.tableData[index].index=index
            this.tableData[index].propor=(this.tableData[index].revenue/total_revenue).toFixed(2)*100+'%'
            this.tableData[index].profit=(this.tableData[index].revenue-this.tableData[index].cost_basic).toFixed(2)
            this.tableData[index].onecount=(this.tableData[index].revenue/this.tableData[index].order_number).toFixed(2)
          }
        })
      },
    },
  }
</script>



