<template>
  <div id="login_bigBox">
    <div class="bg_big abs w100pc h100pc "></div>
    <div class="bg_smo rel w100pc  z-index-2">
      <div class="txal rel z-index-3 smo_bo">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="dslb demo-ruleForm loginFrom txalS" style="width:400px;">
          <p class="f30" style="color: #333333;">星星POS店主后台</p>
          <p class="f13 padd-tb-19" style="color:#a9a9a9">星星便利就在您身边</p>
          <div class="border_b_ee fsb padd-10">
            <img :src="src_0" alt="" class="in_img padd-r-18">
            <input type="text" placeholder="请输入管理员姓名" class="w100pc in_ho f14 fg2" v-model="ruleForm.userName">
          </div>
          <div class="border_b_ee fsb padd-10">
            <img src="../../../static/reset/登录/login_ic_account.png" alt="" class="in_img padd-r-18">
            <input type="password" placeholder="请输入登录密码" class="w100pc in_ho f14 fg2" v-model="ruleForm.password">
          </div>
          <div  class="clear padd-16">
            <div class="rt">
              <el-checkbox v-model="checked" style="color:#919191" class="f13">记住用户名</el-checkbox>
            </div>
          </div>
          <!-- <el-button type="primary" @click="submitForm('ruleForm')" style="width: 100%;" class="but f28 circle">登陆</el-button> -->
          <div class="w100pc">
            <i class="but f20 txal w100pc bor-33 dslb bg-blue white" @click="submitForm('ruleForm')" style=''>登录</i>
          </div>
        </el-form>
      </div>
    </div>


  </div>
</template>
<script>
export default {
  data(){
    return {
      src_0:require('../../../static/reset/登录/login_ic_ssword.png'),
      msg:'',
      ruleForm: {
       userName: '',
       password: '',
       code: '',
     },
     rules: {
      username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
      password: [{required: true, message: '请输入密码', trigger: 'blur'}],
    },
      checked:false,
    }
  },
  mounted(){
    this.getCookie()
  },
  methods: {
    open(s){
      this.$message(s);
    },
    //点击登录调用方法
    submitForm(formName) {
      var name=this.ruleForm.userName;
      var pass=this.ruleForm.password;
      if(name==''){ this.open("请输入正确的用户名")
      return }else if(pass=='') { this.open("请输入正确的密码")
      return }
      var data={
        password:pass,
        name:name,
      };
      this.$api.login(data,res=>{
        // console.log(res)
        if(res.error_code==1000){
          this.open("无该店铺")
          return
        } else if (res.error_code==1001){
          this.open("店铺被冻结")
          return
        } else {
          sessionStorage.setItem("token", res.data[0].token);
          //判断复选框是否被勾选
          if(this.checked==true){
            localStorage.setItem("key", this.$code.addcode(name,'key1'));
            localStorage.setItem("pass", this.$code.addcode(pass,'key2'));
          }
          this.$router.push('/')
        }
      })
    },
    getCookie:function () {
      var key=localStorage.getItem('key')
      var pass=localStorage.getItem('pass')
      if(key && pass){
        this.checked=true
        this.ruleForm.userName=this.$code.pullcode(key,'key1')
        this.ruleForm.password=this.$code.pullcode(pass,'key2')
      }
    },
  },
}
</script>

<style lang="less" scoped>
  .bg_big{
    background: url('../../../static/reset/登录/img_bg.jpg');
    top: 0px;
    left: 0px;
  }
  .bg_smo{
    background: url('../../../static/reset/登录/login_bg.png') no-repeat center center;
    height: 90vh;left: -55px;
    .smo_bo{
      left: 45%;
      top: 25%;
      width: 45%;
      .in_ho{
        border:none;
        outline: none;
      }
      .in_ho::placeholder{
        color: #a9a9a9;
      }
      .in_img{
        width: 20px;
        height: 20px;
      }
      .but{
        height: 47px;
        font-style: inherit;
        line-height: 47px;
      }
    }
  }

</style>



