<template>
  <div id="member_msg_box">
    <!-- header -->
    <div class="fsb mg-10 ">
      <EXport :ex_data='ex_data' class="dslb"></EXport>
      <div class="">
        <el-input v-model="input" placeholder="姓名" style="width:150px;"></el-input>
        <el-input v-model="input2" placeholder="手机号" style="width:150px;"></el-input>
        <el-button type="warning" v-on:click="getdata">查询</el-button>
      </div>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
  </div>
</template>

<script>
import Table from '../../../components/table';
import EXport from '../../../components/export';
export default {
  components:{
    Table,EXport
  },
  data() {
    return {
      input:'',
      input2:'',
      tableData: [],
      table_title:['序号','姓名','手机号','证件号','会员积分','订单总价','订单均价','消费次数','最后消费日期','距今天日期'],
      proplist:['index','uname','phone','idcard','points','total_revenue','to_or','order_num','create_time','delta'],
      table_height:'500',
      ex_data:{}
    }
  },
  watch: {
    'tableData':function (n,o) {
      this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'会员消费信息分析'}
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    getdata(){
      let data={
        name:'',
        phone:'',
      }
      this.$api.memberConsumeAnalysis(data,res=>{
        console.log(res)
        this.tableData=res.data[0].list
        for(let index in this.tableData){
          this.tableData[index].to_or=this.tableData[index].order_num>0?(Number(this.tableData[index].total_revenue)/this.tableData[index].order_num).toFixed(2):''
          this.tableData[index].delta=this.tableData[index].delta+'天'
          this.tableData[index].create_time=this.$code.date(new Date(Number(this.tableData[index].create_time)),0)
        }
      })
    },
  },
}
</script>


<style lang="less" scoped>

</style>




