<template>
  <div id="componenets_table_box">
    <!-- table -->
    <el-table
      :data="tableData"
      :height="table_height"
      border
      style="width:99%">
      <el-table-column v-for="(item,index) in table_title" :key='index'
        :prop="proplist[index]"
        :label="item"
        :sortable='proplist[index]==""?true:false'
        width="">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props:['tableData','table_title','table_height','proplist'],
  data() {
    return {
      // proplist:[]
    }
  },
  created(){
    // console.log(this.proplist)
  },
  watch: {
    'tableData':function (n,o) {
      // console.log(n)
      for(let index in n){
        n[index].index=Number(index)+1
      }
      // console.log(this.tableData)
    }
  },
  methods: {

  },
}
</script>


<style lang="less" scoped>

</style>



