<template>
  <div id="shopping_stor_chart_box">
      <!-- header -->
      <div class="fsb mg-10 ">
        <EXport :ex_data='ex_data'></EXport>
        <div class="">
          <el-select v-model="value1" placeholder="" >
            <el-option
              label="全部分类"
              value="1000">
            </el-option>
            <el-option
              v-for="item in options1"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
          <el-select v-model="value2" placeholder="" >
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-input v-model="input1" placeholder="商品名称" style="width:150px;"></el-input>
          <el-input v-model="input2" placeholder="商品条码" style="width:150px;"></el-input>
          <el-button type="warning" v-on:click="getdata">查询</el-button>
        </div>
      </div>
      <!-- table -->
      <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
      <PageCoat @getpage='getpage' :total='total'></PageCoat>
  </div>
</template>

<script>
import Table from '../../../components/table';
import PageCoat from '../../../components/pagination';
import EXport from '../../../components/export';
export default {
  components:{
    Table,PageCoat,EXport,
  },
  data() {
    return {
      options1: [],
      options2: [
        {
          value: '1',
          label: '上架'
        }, {
          value: '0',
          label: '未上架'
        }, {
          value: '1000',
          label: '全部'
        }
      ],
      tableData: [],
      table_title:['序号','商品名称','商品编码','商品分类','库存','单位','计价方式','货架号','是否上架','是否快捷'],
      proplist:['index','goods_name','goods_sn','cat_id','repertory','unit','type','place_code','is_forsale','is_short'],
      table_height:'500',
      value1: '1000',
      value2: '1000',
      input1:'',
      input2:'',
      total:0,
      onepage:10,
      pagenow:1,
      ex_data:{}
    }
  },
  watch: {
    'tableData':function (n,o) {
      this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'库存查询'}
    }
  },
  created() {
    this.getclass()
  },
  methods: {
    getclass(){
      let data1={}
      this.$api.getcategorylist(data1,res=>{
        this.options1=res.data[0]
        this.getdata()
      })
    },
    getdata(){
      let data={
        record_no:this.onepage,
        page_no:this.pagenow,
        cat_id:this.value1,
        goods_name:this.input1,
        goods_sn:this.input2,
        is_forsale:this.value2,
      }
      this.$api.getinventorylist(data,res=>{
        this.total=res.data[0].filter.total_number
        this.tableData=res.data[0].list
        for(let index in this.tableData){
          this.tableData[index].index=Number(index)+1;
          this.tableData[index].type=this.tableData[index].type==0?'计件':'计重';
          this.tableData[index].is_forsale=this.tableData[index].is_forsale==0?'否':'是';
          this.tableData[index].is_short=this.tableData[index].is_short==0?'否':'是';
          this.tableData[index].cat_id=this.$code.classFor(this.options1,this.tableData[index].cat_id);
        }
      })
    },
    getpage(data){
      switch (data.type) {
        case 0:
          this.onepage=data.val
          this.getdata()
          break;
        case 1:
          this.pagenow=data.val
          this.getdata()
          break;
        default:
          break;
      }
    },
  },
}
</script>




