<template>
  <div id="Create_data_box">
    <!-- header 1 -->
    <div class="padd-tb-10">
      <el-button :type="btuType" @click="edit" class="mg-l-15 ">编辑</el-button>
    </div>
    <!-- content 1 -->
    <div class="" >
      <h5 class="padd-20 mg-l-15 bg-gray">店铺信息</h5>
      <div class="fsa mg-10">
        <p><span>商铺名称：</span><el-input class="inpu" :disabled="isedit?true:false" v-model="form.store_name" placeholder="商铺名称"></el-input></p>
        <p><span>商铺编码：</span><el-input class="inpu" disabled v-model="form.store_code" placeholder="由后台生成"></el-input></p>
      </div>
      <div class="fsa mg-10">
        <p>
          <span>商铺地址：</span>
          <el-select v-model="address0" :disabled="isedit?true:false" class="sele" @change="changeone" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.region_id"
              :label="item.region_name"
              :value="item.region_id">
            </el-option>
          </el-select>
          <el-select v-model="address1" :disabled="isedit?true:false" class="sele" @change="changetwo" placeholder="请选择">
            <el-option
              v-for="item in options1"
              :key="item.region_id"
              :label="item.region_name"
              :value="item.region_id">
            </el-option>
          </el-select>
          <el-select v-model="address2" :disabled="isedit?true:false" @change='area' class="sele" placeholder="请选择">
            <el-option
              v-for="item in options2"
              :key="item.region_id"
              :label="item.region_name"
              :value="item.region_id">
            </el-option>
          </el-select>
        </p>
        <p><span>详细地址：</span><el-input :disabled="isedit?true:false" class="inpu" v-model="form.address" placeholder="详细地址"></el-input></p>
      </div>
      <div class=" mg-10">
        <p><span class="mg-l-42 dslb">营业执照编号：</span><el-input :disabled="isedit?true:false" class="inpu" v-model="form.business_licence_no" placeholder=""></el-input></p>
      </div>
    </div>
    <!-- content 2 -->
    <div class="" >
      <h5 class="padd-20 mg-l-15 bg-gray">店主信息</h5>
      <div class="fsa mg-10">
        <p><span>店主姓名：</span><el-input :disabled="isedit?true:false" class="inpu" v-model="form.realname" placeholder=""></el-input></p>
        <p><span>联系方式：</span><el-input :disabled="isedit?true:false" class="inpu" v-model="form.phone" placeholder=""></el-input></p>
      </div>
    </div>
    <!-- content 3 -->
    <div class="" >
      <h5 class="padd-20 mg-l-15 bg-gray">收款信息</h5>
      <div class="fsa mg-10">
        <p><span>开户行：</span><el-input class="inpu" disabled v-model="bank_name0" placeholder=""></el-input></p>
        <p><span>账户名：</span><el-input class="inpu" disabled v-model="form.account_name" placeholder=""></el-input></p>
      </div>
      <div class=" mg-10">
        <p><span class="mg-l-42 dslb">收款账号：</span><el-input disabled class="inpu" v-model="form.account_no" placeholder=""></el-input></p>
      </div>
    </div>
    <!-- foot -->
    <div v-show="!isedit" class="fsa">
      <el-button @click="again">取消</el-button>
      <el-button type="primary" @click="edit_store(form)">确认修改</el-button>
    </div>
  </div>
</template>

<script>
  let id = 0;
  export default {
    data() {
      return {
        btuType:'primary',
        isedit:true,
        options: [],
        options1: [],
        options2: [],
        address0:'',
        address1:'',
        address2:'',
        bank_name0:'',
        form:{
          store_name:'',
          store_code:'',
          address:'',
          business_licence_no:'',
          uname:'',
          realname:'',
          phone:'',
          province_id:'',
          city_id:'',
          area_id:'',
          bank_id:'',
          bank_name:'',
          account_name:'',
          account_no:'',
        }
      };
    },
    computed: {

    },
    watch: {
      'form.bank_name':function (n,o) {
        console.log(n)
        this.form.bank_name=n
      }
    },
    created() {
      this.getstoredata()
    },
    updated() {

    },
    methods: {
      getbank(i){
        let bank_data={}
        console.log(this.form)
        this.$api.bank(bank_data,res=>{
           for(let item of res.data[0].list){
            if(item.id==i){
              this.bank_name0=item.name;
              // console.log(this.form.bank_name)
              return false
            }
          }
        })
      },
      changeone(){
        this.form.province_id=this.address0
        console.log(this.form.province_id)
        this.getcity(this.form.province_id)
        this.address1=''
        this.address2=''
        this.options2=[]
      },
      changetwo(){
        this.address2=''
        this.form.city_id=this.address1
        console.log(this.form.city_id)
        this.getarea(this.form.city_id)
      },
      area(){
        this.form.area_id=this.address2
        console.log(this.form.area_id)
      },
      getstoredata(){
        let _this=this
        let data={}
        this.$api.getStoreData(data,res=>{
          this.form=res.data[0]
          this.form.bank_name=''
          this.address0=this.form.province_id
          this.address1=this.form.city_id
          this.address2=this.form.area_id
          this.getprovince()
          // debugger
          this.getcity(this.form.province_id)
          this.getarea(this.form.city_id)
          this.getbank(this.form.bank_id)
        })
      },
      getprovince(){
        let city_data={}
        this.$api.province(city_data,res=>{
          console.log(res)
          this.options=res.data.list
        })
      },
      getcity(pro_id){
        let city_data={
          pro_id
        }
        this.$api.city(city_data,res=>{
          console.log(res)
          this.options1=res.data.list
        })
      },
      getarea(city_id){
        let city_data={city_id}
        this.$api.area(city_data,res=>{
          console.log(res)
          this.options2=res.data.list
        })
      },
      edit(){
        this.btuType='danger';
        this.isedit=false;
      },
      again(){
        this.btuType='primary';
        this.isedit=true;
      },
      edit_store(form){
        console.log(form)
        let store_data={
          id:'',
          store_name:'',
          province_id:'',
          city_id:'',
          area_id:'',
          address:'',
          phone:'',
          bank_id:'',
          account_name:'',
          account_no:'',
          business_licence_no:'',
          realname:''
        }
        form.id=form.local_id
        store_data=form
        this.$api.updatestore(store_data,res=>{
          this.getstoredata()
          this.again()
        })
      },
    },
  };
</script>

<style lang="less" scoped>
.inpu{
  width: 60%;
}
p{
  width:45%;
}
.sele{
  width: 100px;
}
</style>





