export default {
  state:{
    show:false,
  },
  getters:{
    show:state=>state.show,
  },
  mutations:{
    updateShow:(state,payload)=>{
      state.show=payload;
    },
  },
  actions:{
    updateShow:({commit},payload)=>{
      commit('updateShow',payload);
    },
  },
}



