<template>
  <div id="staffCrea">
    <!-- header -->
    <div class="fsb mg-10">
      <div>
        <el-button type="primary" @click="S_add('新增')">新增收银员</el-button>
        <el-button type="primary" @click="toRold">员工角色管理</el-button>
        <el-button type="danger" @click.stop="del">删除</el-button>
      </div>
      <div>
        <el-select v-model="value1" placeholder="" >
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-input v-model="input" placeholder="账号" style="width:150px;"></el-input>
        <el-button type="warning" @click="getdata">查询</el-button>
      </div>
    </div>
    <!-- table -->
    <el-table
        ref="multipleTable"
        :data="tableData"
        height="400"
        tooltip-effect="dark"
        style="width: 98%"
        :row-class-name="tableRowClassName"
        @selection-change="handleSelectionChange">
        <el-table-column
        type="selection"
        width="55">
        </el-table-column>
        <el-table-column
        prop="id"
        label="序号"
        width="">
        </el-table-column>
        <el-table-column label="操作"  width="240" >
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row,'编辑')">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row,'重置密码')" v-show="scope.row.rank==1">重置密码</el-button>
          </template>
        </el-table-column>
        <el-table-column
        prop="store_name"
        label="门店"
        width="">
        </el-table-column>
        <el-table-column
        prop="realname"
        label="姓名"
        width="">
        </el-table-column>
        <el-table-column
        prop="uname"
        label="账号"
        show-overflow-tooltip>
        </el-table-column>
        <el-table-column
        label="所属角色"
        width="">
        <template slot-scope="scope">{{ scope.row.rank==1?'雇员':'店主' }}</template>
        </el-table-column>
        <el-table-column label="是否启用" width="100" prop="radio">
          <template slot-scope="scope">
             <p v-show="scope.row.is_ok">启用</p>
             <p v-show="!scope.row.is_ok">禁用</p>
          </template>
        </el-table-column>
    </el-table>
    <PageCoat @getpage='getpage' :total='total'></PageCoat>
    <!-- add -->
    <div class="fixed" v-show="show_add" style="z-index: 2;top:0;left:0;width:100vw;height:100vh;"></div>
    <div v-show="show_add" class="fixed bg-gray bor-14" style="z-index: 3;top:20%;left:40%;width:500px;">
      <div class='fsb mg-20'>
        <p><i class='dslb bg-skyblue' style='width:5px;height:16px;'></i><span class=''>{{butName}}</span></p>
        <p class="f24 gray" v-on:click='S_add'>X</p>
      </div>
      <div v-show="butName!=='重置密码'" class="txal mg-20">
        <div class="mg-t-10">
          <p class='dslb mu_one'>账号</p>
          <p class='dslb mu_two'>
            <el-input class="w100pc" :disabled="butName=='编辑'?true:false" v-model="formOne.code" placeholder="请输入字母或数字，不含特殊字符" ></el-input>
          </p>
        </div>
        <div class="mg-t-10">
          <p class='dslb mu_one'>密码</p>
          <p class='dslb mu_two'>
            <el-input class="w100pc" :disabled="butName=='编辑'?true:false" v-model="formOne.passw" placeholder="请输入6-11位字母或数字，不含特殊字符"></el-input>
          </p>
        </div>
        <div class="mg-t-10">
          <p class='dslb mu_one'>姓名</p>
          <p class='dslb mu_two'>
            <el-input class="w100pc" v-model="formOne.name" placeholder="不含数字或特殊字符" ></el-input>
          </p>
        </div>
        <div class="mg-t-10">
          <p class='dslb mu_one'>角色</p>
          <p class='dslb mu_two'>
            <el-select class="w100pc" v-model="formOne.rold" placeholder="" >
              <el-option
                v-for="item in options3"
                :key="item.value"
                :label="item.label"
                :value="item.value+''">
              </el-option>
            </el-select>
          </p>
        </div>
      </div>
      <div v-show="butName=='重置密码'" class="txal mg-20">
        <div class="mg-t-10">
          <p class='dslb mu_one'>重置密码</p>
          <p class='dslb mu_two'>
            <el-input class="w100pc" v-model="formOne.oldpw"  placeholder="请输入6-11位字母或数字，不含特殊字符，区分大小写" ></el-input>
          </p>
        </div>
        <div class="mg-t-10">
          <p class='dslb mu_one'>确认密码</p>
          <p class='dslb mu_two'>
            <el-input class="w100pc" v-model="formOne.newpw"  placeholder="请输入6-11位字母或数字，不含特殊字符，区分大小写"></el-input>
          </p>
        </div>
      </div>
      <div class="fsa mg-20">
        <el-button v-on:click='S_add'>取消</el-button>
        <el-button type="primary" @click="formone(formOne,butName)">提交</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import PageCoat from '../../../components/pagination'
import code from '../../../common/code'
export default {
  components:{
    PageCoat,
  },
  data() {
    return {
      value1:'',
      value2:'',
      butName:'',
      tableData: [],
      options1: [
        {
          value: '0',
          label: '全部'
        }, {
          value: '1',
          label: '启用'
        }, {
          value: '2',
          label: '禁用'
        }
      ],
      options3: [
          {
            value: '0',
            label: '店主'
          }, {
            value: '1',
            label: '店员'
          }
      ],
      value1:'0',
      input:'',
      crera_ind:[],
      resource:'',
      radio:'',
      show_add:false,
      formOne:{
        code:'',
        passw:'',
        name:'',
        rold:'0',
        oldpw:'',
        newpw:'',
      },
      reset_formOne:{
        code:'',
        passw:'',
        name:'',
        rold:'0',
        oldpw:'',
        newpw:'',
      },
      onepage:10,
      pagenow:1,
      total:0,
      reset_id:''
    }
  },
  created() {
    this.getdata()
  },
  watch: {

  },
  updated() {
    // console.log(this.value1)
  },
  methods: {
    getdata(){
      let status=''
      switch (this.value1) {
        case '1':
          status='1'
          break;
        case '2':
          status='0'
          break;

        default:
          status=''
          break;
      }
      var data={
        page_no:this.pagenow,
        record_no:this.onepage,
        status,
        name:this.input,
      }
      this.$api.getStaffList(data,res=>{
        if(res.data.length==0){return false}
        this.total=Number(res.data[0].filter.total_number)
        this.tableData=res.data[0].list
        for(let item of this.tableData){
          if(item.is_active==0){
            item.is_ok=false
          }else{
            item.is_ok=true
          }
        }
      })
    },
    tableRowClassName({row, rowIndex}) {
      // console.log(rowIndex)
      for(let i of this.crera_ind){
        if (rowIndex === i.index) {
          return 'bg-orange';
        }
      }
      return '';
    },
    handleSelectionChange(val) {
        console.log(val)
        this.crera_ind=val
    },
    handleEdit(index, row,type) {
      console.log(index, row);
      this.show_add=!this.show_add;
      this.butName=type;
      this.formOne={
        code:row.store_code,
        passw:row.password,
        name:row.realname,
        rold:row.rank+'',
        id:row.id,
      }
    },
    handleDelete(index, row,type) {
      console.log(index, row,type);
      this.show_add=!this.show_add;
      this.butName=type;
      this.reset_id=row.id
    },
    del_ms(){
      let del_arr=[]
      let dal_res={}
      for(let i of this.crera_ind){
        del_arr.push(i.id)
      }
      console.log(del_arr.toString())
      del_arr=del_arr.toString()
      let del_data={staff_id:del_arr}
      this.$api.delStaff(del_data,res=>{
        if(res.code==1){
          this.$message('删除成功')
          this.getdata()
        }else{
          this.$message('删除失败')
        }
      })
    },
    del(){
      let del_this=this
      if(this.crera_ind.length>0){
        this.$confirm('确定删除该记录?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            callback:function(action, instance){
              if(action=='confirm'){
                del_this.del_ms()
              }
            }
          })
      }else{
        this.$message({
          type: 'info',
          message: '请选择删除记录',
          duration:1000
        });
      }
    },
    S_add(type){
      this.formOne=this.$code.copyobj(this.reset_formOne)
      this.show_add=!this.show_add;
      this.butName=type;
    },
    formone(s,butName){
      console.log(s)
      let par=/[0-9|a-z|A-Z]/g
      if(butName=='重置密码' && s.newpw.match(par) && s.oldpw.match(par)){
        // if(s.newpw.match(par).length!=s.newpw.length ||s.oldpw.match(par).length!=s.oldpw.length|| s.newpw.length<6 ||s.newpw.length>11 ||s.newpw!=s.oldpw){this.$message('请确认填写信息'); return false}
        if(!s.newpw.match(/^[A-Za-z0-9]+$/g) || !s.newpw|| s.newpw.length<6 ||s.newpw.length>11 || s.newpw!=s.oldpw){this.$message('请确认密码格式是否规范');return false}
        let reset_data={
          password:this.formOne.newpw,
          password_confirm:this.formOne.newpw,
          staff_id:this.reset_id
        }
        this.$api.resetStorePassword(reset_data,res=>{
          this.$message(res.message)
          if(res.code==1){
            this.show_add=false
            this.getdata()
          }
        })
      }else{this.$message('请完整填写信息')}
      if(butName=='新增'){
        if(!s.code.match(/^[A-Za-z0-9]+$/g)|| !s.code){this.$message('请正确填写账号');return false}
        if(!s.passw.match(/^[A-Za-z0-9]+$/g)|| !s.passw|| s.passw.length<6 ||s.passw.length>11 ){this.$message('请正确填写密码');return false}
        if(!s.name.match(/^[\u4E00-\u9FA5A-Za-z]+$/)|| !s.name){this.$message('请正确填写姓名');return false}
        let add_data={
          uname:this.formOne.code,
          password:this.formOne.passw,
          rank:this.formOne.rold,
          realname:this.formOne.name
        }
        this.$api.createstaff(add_data,res=>{
          this.$message(res.message)
          if(res.code==1){
            this.getdata()
            this.show_add=false
          }
        })
      }
      if(butName=='编辑'){
        if(!s.name.match(/^[\u4E00-\u9FA5A-Za-z]+$/)|| !s.name){this.$message('请正确填写姓名');return false}
        console.log(s)
        let edit_data={
          staff_id:s.id,
          realname:s.name,
          rank:s.rold,
        }
        this.$api.updateStaff(edit_data,res=>{
          this.$message(res.message)
          if(res.code==0){return false}
          this.getdata()
          this.show_add=false
        })
      }
    },
    toRold(){
      this.$router.push('/staff/staffCen/roldCreate')
    },
    getpage(data){
      switch (data.type) {
        case 0:
          this.onepage=data.val
          this.getdata()
          break;
        case 1:
          this.pagenow=data.val
          this.getdata()
          break;
        default:
          break;
      }
    }
  }
}
</script>
<style lang="less" scoped>
.mu_one{
  background: #999;
  width: 100px;
  line-height: 30px;
  position: relative;
  top: 3px;
}
.mu_two{
  background: #fff;
   width: 300px;
  line-height: 30px;
  color:blue;
}
</style>




