import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);
import Index from '@/pages/home';
import Prict from '@/prict/index';
import Login from '@/pages/login/login';
import SaleView from '@/pages/sale/view';
import SaleView0 from '@/pages/sale/view_0';
import Trend from '@/pages/sale/data/trend';
import One from '@/pages/sale/data/one';
import Sale from '@/pages/sale/data/sale';
import Proportion from '@/pages/sale/data/proportion';
import orderData from '@/pages/sale/order/data';
import recordDay from '@/pages/sale/record/day';
import recordClass from '@/pages/sale/record/class';
import shoppingOver from '@/pages/shopping/overview';
import centerClass from  '@/pages/shopping/center/class';
import centerCreate from '@/pages/shopping/center/create';
import shoppingData from '@/pages/shopping/center/data';
import storeChange from  '@/pages/shopping/store/change';
import storeChart from   '@/pages/shopping/store/chart';
import storeDel from     '@/pages/shopping/store/del';
import storeHistory from '@/pages/shopping/store/history';
import staffCreate from '@/pages/staff/staffCen/create';
import staffSucc from '@/pages/staff/staffCen/succ';
import roldCreate from '@/pages/staff/staffCen/roldCreate';
import memView from '@/pages/member/allview';
import memCreate from '@/pages/member/center/create';
import memMsg from '@/pages/member/table/massge';
import memOnce from '@/pages/member/table/once';
import memScor from '@/pages/member/table/scor';
import OverData from '@/pages/data';
// import NotFound from '@/components/404';
import createData from '@/pages/create/data';
import createsysCra from '@/pages/create/sysCra';
import createuseDate from '@/pages/create/useDate';

const router = new Router({
  routes:[
    {
      path:'/',
      name:'index',
      component:Index
    },
    {
      path:'/prict',
      name:'prict',
      component:Prict
    },
    {
      path:'/login',
      name:'login',
      component:Login
    },
    {
      path:'/sale/view',
      name:'saleview',
      component:SaleView
    },
    {
      path:'/sale/view_0',
      name:'saleview0',
      component:SaleView0
    },
    {
      path:'/sale/data/trend',
      name:'trend',
      component:Trend
    },
    {
      path:'/sale/data/one',
      name:'one',
      component:One
    },
    {
      path:'/sale/data/sale',
      name:'sale',
      component:Sale
    },
    {
      path:'/sale/data/proportion',
      name:'proportion',
      component:Proportion
    },
    {
      path:'/sale/order/data',
      name:'orderData',
      component:orderData
    },
    {
      path:'/sale/record/day',
      name:'recordDay',
      component:recordDay
    },
    {
      path:'/sale/record/class',
      name:'recordClass',
      component:recordClass
    },
    {
      path:'/shopping/overview',
      name:'overview',
      component:shoppingOver
    },
    {
      path:'/shopping/center/class',
      name:'centerClass',
      component:centerClass
    },
    {
      path:'/shopping/center/create',
      name:'centerCreate',
      component:centerCreate
    },
    {
      path:'/shopping/center/data',
      name:'shoppingData',
      component:shoppingData
    },
    {
      path:'/shopping/store/change',
      name:'storeChange',
      component:storeChange
    },
    {
      path:'/shopping/store/chart',
      name:'storeChart',
      component:storeChart
    },
    {
      path:'/shopping/store/del',
      name:'storeDel',
      component:storeDel
    },
    {
      path:'/shopping/store/history',
      name:'storeHistory',
      component:storeHistory
    },
    {
      path:'/staff/staffCen/create',
      name:'staffCreate',
      component:staffCreate
    },
    {
      path:'/staff/staffCen/succ',
      name:'staffSucc',
      component:staffSucc
    },
    {
      path:'/staff/staffCen/roldCreate',
      name:'roldCreate',
      component:roldCreate
    },
    {
      path:'/member/allview',
      name:'memView',
      component:memView
    },
    {
      path:'/member/center/create',
      name:'memCreate',
      component:memCreate
    },
    {
      path:'/member/table/massge',
      name:'memMsg',
      component:memMsg
    },
    {
      path:'/member/table/once',
      name:'memOnce',
      component:memOnce
    },
    {
      path:'/member/table/scor',
      name:'memScor',
      component:memScor
    },
    {
      path:'/data',
      name:'OverData',
      component:OverData
    },
    {
      path:'/create/data',
      name:'createData',
      component:createData
    },
    {
      path:'/create/sysCra',
      name:'createsysCra',
      component:createsysCra
    },
    {
      path:'/create/useDate',
      name:'createuseDate',
      component:createuseDate
    },
    {
      path:'*',
      redirect: "/"
    },
  ]
});
export default router;




