<template>
  <div id="Create_sysCra_box">
    <div class="mg-15">
      <el-button type="primary" @click="S_reset">重置</el-button>
    </div>
    <div class="txal">
     <p class="dslb mg-10" style="width:45%;"><span class="mg-l-5"> 登 录 名 ： </span><el-input class="inpu" disabled v-model="name" placeholder=""></el-input></p>
     <br />
     <p class="dslb mg-10" style="width:45%;"><span>登录密码：</span><el-input type="password" disabled class="inpu" v-model="password" placeholder=""></el-input></p>
    </div>
     <!-- reset -->
    <div v-show="isreset" class="fixed bg-gray  bor-14" style="z-index: 1;top:20%;left:40%;width:500px;height:285px;">
      <div class='fsb mg-20'>
        <p><i class='dslb bg-skyblue' style='width:5px;height:16px;'></i><span class='gray'>重置密码</span></p>
        <p class="f24 gray" v-on:click='S_reset'>X</p>
      </div>
      <div class="txal mg-20">
        <div class="mg-10">
          <p class='dslb mu_one'>原密码</p>
          <p class='dslb mu_two'><el-input class="" type="password" v-model="form.old_password" placeholder=""></el-input></p>
        </div>
        <div class="mg-10">
          <p class='dslb mu_one'>新密码</p>
          <p class='dslb mu_two'><el-input class="" type="password" v-model="form.password" placeholder=""></el-input></p>
        </div>
        <div class="mg-10">
          <p class='dslb mu_one'>重复新密码</p>
          <p class='dslb mu_two'><el-input class="" type="password" v-model="form.password_confirm" placeholder=""></el-input></p>
        </div>
      </div>
      <div class="fsa mg-20">
        <el-button v-on:click='S_reset'>取消重置</el-button>
        <el-button type="primary" @click="reset(form)">确认提交</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name:'',
      password:'',
      form:{
        old_password:'',
        password:'',
        password_confirm:'',
        staff_id:''
      },
      isreset:false,
    }
  },
  created(){
    this.getSession()
  },
  methods: {
    reset(form){
      console.log(form)
      this.$api.resetStorePassword(form,res=>{
        this.$message(res.message)
        console.log(res)
        this.isreset=false
        if(res.code==1){
          this.$router.push('/login')
        }
      })
    },
    S_reset(){
      this.isreset=!this.isreset
    },
    getSession(){
      var key=sessionStorage.getItem('key')
      var pass=sessionStorage.getItem('pass')
      this.name=this.$code.pullcode(key,'key1')
      this.password=this.$code.pullcode(pass,'key2')
    },

  },
}
</script>

<style lang="less" scoped>
.inpu{
  width: 60%;
}

.mu_one{
  background: #999;
  width: 100px;
  line-height: 30px;
}
.mu_two{
  background: #fff;
   width: 300px;
  line-height: 30px;
  color:blue;
}
</style>




