<template>
  <div class="components_HC_box">
    <!-- header -->
    <div class="fsb padd-20 mg-10 bob bg-fff">
        <h1 class="f17">{{title}}</h1>
        <div class="fsa f16" v-if='v_flag' style="width: 500px;">
            <span class="padd-rl-12 te_de blue_0" :class="[index_top==0?'n_boc':'']" v-on:click="again(0)">今日</span>
            <span class="padd-rl-12 te_de blue_0" :class="[index_top==1?'n_boc':'']" v-on:click="again(1)">昨日</span>
            <span class="padd-rl-12 te_de blue_0" :class="[index_top==2?'n_boc':'']" v-on:click="again(2)">本周</span>
            <span class="padd-rl-12 te_de blue_0" :class="[index_top==3?'n_boc':'']" v-on:click="again(3)">本月</span>
            <span class="padd-rl-12 te_de blue_0" :class="[index_top==4?'n_boc':'']" v-on:click="again(4)">自定义</span>
        </div>
        <div class="fsa" v-if='!v_flag'>
            <el-date-picker
              v-model="value1"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd HH:mm:ss">
            </el-date-picker>
            <el-button type="warning" class="mg-rl-10" @click="chart">查询</el-button>
            <!-- <span class="padd-rl-12 te_de" v-on:click="again(4)">+</span> -->
            <img src='../../static/header_img/header_3.png' style="transform: rotate(45deg)" v-on:click="again(4)" alt="">
        </div>
    </div>
  </div>
</template>

<script>
export default {
  props:['title'],
  data() {
    return {
      v_flag:true,
      value1:[],
      value2:'',
      index_top:0,
    }
  },
  methods: {
    open(msg){
      this.$message(msg)
    },
    again(a){
      this.index_top=a
      if(this.index_top==4){
        this.v_flag=!this.v_flag
      }
      this.$emit('headerC',a)
    },
    chart(){
      if(this.value1==''){
        this.open('请完整输入日期')
        return false;
      }
      let form={
        value1:this.value1,
      }
      this.$emit('chart',form)
      // if(form.value1<form.value2){
      //   this.$emit('chart',form)
      // }else{
      //   console.log('请正确输入时间区间')
      // }
    }
  },
}
</script>


<style lang="less" scoped>
.n_boc{
  width:53px;
  height:23px;
  line-height: 23px;
  text-align: center;
  color: white;
  background:linear-gradient(0deg,rgba(0,102,255,1),rgba(146,190,255,1));
  border-radius:11px;
}
</style>




