<template>
  <div id='record_box'>
    <!-- content -->
    <div class="fsb mg-10">
      <div>
        <el-button type="danger" @click="goView">营业概况</el-button>
        <el-button type="primary">交接班记录</el-button>
        <EXport :ex_data='ex_data' class="dslb"></EXport>
      </div>
      <div>
        <el-date-picker
          v-model="value1"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
        <el-button type="warning" @click="getdata">查询</el-button>
      </div>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist='proplist'></Table>
    <PageCoat @getpage='getpage' :total='total'></PageCoat>
  </div>
</template>
<script>
import Table from '../../../components/table';
import PageCoat from '../../../components/pagination';
import EXport from '../../../components/export';
export default {
  components:{
    Table,PageCoat,EXport
  },
  data() {
    return {
      value1:[this.$code.yestedate(),this.$code.todate()],
      value2:'',
      tableData: [],
      table_title:['开始时间','结束时间','收银员','收银总额','现金支付','银联支付','支付宝支付','微信支付'],
      proplist:['start_time','end_time','realname','total_price','xj','yl','zfb','wx'],
      table_height:'600',
      total:0,
      onepage:10,
      pagenow:1,
       ex_data:{}
    }
  },
  watch: {
    'tableData':function (n,o) {
      this.ex_data={tHeader:this.table_title,filterVal:this.proplist,tableData:this.tableData,title:'交班记录'}
    }
  },
  created(){
    this.getdata()
  },
  mounted() {

  },
  methods: {
    goView(){
      this.$router.push('/sale/view')
    },
    getdata(){
      let data={
        record_no:this.onepage,
        page_no:this.pagenow,
        start_date:this.value1[0],
        end_date:this.value1[1],
        staff_id:''
      }
      this.$api.getClassList(data,res=>{
        this.total=res.data[0].filter.total_number
        this.tableData=res.data[0].list
        for(let index in this.tableData){
          this.tableData[index].start_time=this.$code.date(new Date(Number(this.tableData[index].start_time)),1)
          this.tableData[index].end_time=this.$code.date(new Date(Number(this.tableData[index].end_time)),1)
        }
      })
    },
    getpage(data){
      switch (data.type) {
        case 0:
          this.onepage=data.val
          this.getdata()
          break;
        case 1:
          this.pagenow=data.val
          this.getdata()
          break;
        default:
          break;
      }
    }
  },
}
</script>




