            </el-table-column>
            <el-table-column
              prop="cripro"
              label="环比">
            </el-table-column>
          </el-table>
          <el-table
            :header-cell-style="{background:'#eef1f6'}"
            v-show='!is_three'
            :data="data.rank_by_no"
            stripe
            style="width: 99.9%">
            <el-table-column
              prop="goods_name"
