              >
            </el-table-column>
            <el-table-column
              prop="goods_total_no"
              label="数量"
              >
            </el-table-column>
            <el-table-column
              prop="propor"
              label="占比">
            </el-table-column>
            <el-table-column
              prop="cripro"
              label="环比">
            </el-table-column>
          </el-table>
      </div>
      <div class='bob mg-10 padd-b-20 fsb'>
        <!-- fore -->
        <div class="bob  bg-fff padd-b-20" style='width:48%;'>
          <div class="fsb padd-25 bor_b_gray_eee">
            <p class='fsb f16 blue_0'><span class='dslb bu_box'></span>会员</p>
            <router-link to="/member/allview">更多</router-link>
          </div>
          <div class="txalS padd-30 mg-l-30 mg-r-30 mg-b-5 lh-50" >
            <div class='padd-l-24 mg-b-10 fsb'>
                <span class='fg2'>新增会员{{data.new_member_no}}人 </span>
                <span class='fg1'>消费{{data.new_member_order_no}}单</span>
                <span class='fg1'>占比{{(data.new_member_no/(data.new_member_no+data.old_member_no))*100 | toUSD}}%</span>
