<script>
import HeaderChaqnge from '../../components/headerChange.vue'
export default {
  components:{
    HeaderChaqnge,
  },
  name:'index',
  data(){
    return {
      table:[
        {
          goods_name:'fd',
          goods_total_no:654
        },
        {
          goods_name:'fd',
          goods_total_no:654
        },
        {
          goods_name:'fd',
          goods_total_no:654
        },
        {
          goods_name:'fd',
          goods_total_no:654
        },
        {
          goods_name:'fd',
          goods_total_no:654
        },
      ],
      title:'整体概况',
      is_today:true,
      is_yesterday:false,
      is_week:false,
      is_month:false,
      value_date:[],
      data:{},
      is_three:true,
    }
  },
  created() {
    this.getdata()
  },
  mounted() {
  },
  methods: {
    getheaderC(data){
      // console.log(data)
      switch (data) {
        case 0:
          this.is_today=true;
          this.is_yesterday=false;
          this.is_week=false;
          this.is_month=false;
          this.value_date=[]
          this.getdata()
          break;
        case 1:
          this.is_yesterday=true;
          this.is_today=false;
          this.is_week=false;
          this.is_month=false;
          this.value_date=[]
          this.getdata()
          break;
        case 2:
          this.is_week=true;
          this.is_yesterday=false;
          this.is_today=false;
          this.is_month=false;
          this.value_date=[]
          this.getdata()
          break;
        case 3:
          this.is_month=true;
          this.is_week=false;
          this.is_yesterday=false;
          this.is_today=false;
          this.value_date=[]
          this.getdata()
          break;
        case 4:
          this.is_today=false;
          this.is_yesterday=false;
          this.is_week=false;
          this.is_month=false;
          this.value_date=[]
          break;
        default:
          break;
      }
    },
    isThree(type){
      this.is_three=!this.is_three
    },
    chart(data){
      // console.log(data)
      this.value_date=data.value1
      this.getdata()
    },
    getdata(){
      let data={
        start_date:this.value_date.length>0?this.value_date[0]:'',
        end_date:this.value_date.length>0?this.value_date[1]:'',
        is_today:this.is_today?1:2,
        is_yesterday:this.is_yesterday?1:2,
        is_week:this.is_week?1:2,
        is_month:this.is_month?1:2,
      }
      console.log(data)
      this.$api.home(data,res=>{
        console.log(res)
        if(res.data.length==0){return false}
        this.data=res.data[0].list
      })
    }
  },
}
</script>

<style lang="less" scoped>
  #bigBox{
    text-align: center;
    .bu_box{
      width:23px;
      height:23px;
      background:rgba(78,159,247,1);
      margin-right: 10px;
    }
    .two_box_0{
      // width:1170px;
      height:121px;
      background:rgba(255,255,255,1);
      border:1px solid rgba(238,238,238,1);
      .i_0{
        height:65px;
        border:1px solid rgba(238,238,238,1);
      }
      .img_box{
        width:65px;
        height:65px;
        background:rgba(240,246,255,1);
        border-radius:50%;
      }
      .tx_box{
        margin-left: 30px;
        line-height: 30px;
      }
    }
    .three_00_i{
      width:5px;
      height:18px;
      background:rgba(0,102,255,1);
      border-radius:2px;
    }
    .blue_box_0{
      width:58px;
      height:25px;
      background:rgba(0,102,255,1);
      color:rgba(255,255,255,1);
    }
    .menbenr_box_0{
      width:14px;
      height:14px;
      background:rgba(251,78,115,1);
      border-radius:50%;
    }
  }
</style>




