<template>
  <div id="trend_box">
    <!-- header -->
    <div class="fsb padd-10 bg-gray">
      <h3>整体概况</h3>
      <div class="fsa" >
        <div class="mg30">
          <el-date-picker
          v-model="value1"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
        </div>
        <el-button type="warning" v-on:click="getdata">统计分析</el-button>
      </div>
    </div>
    <!-- content1 -->
     <div class='txalS mg-25'>
        <i class="dslb h100pc" style="border: 2px solid #d2cbcb;height: 20px;"></i>
        <h1 class='dslb'>销售趋势分析</h1>
      </div>
      <div id="onechart" style="height:300px;width:98%;"></div>
      <!-- table -->
      <div class="txalE padd-10"><EXport :ex_data='ex_data1' class="dslb"></EXport></div>

      <Table :tableData='tableData' :proplist='proplist1' :table_title='table_title' :table_height='table_height'></Table>
    <!-- content2 -->
     <div class='txalS mg-25'>
        <i class="dslb h100pc" style="border: 2px solid #d2cbcb;height: 20px;"></i>
        <h1 class='dslb'>销售总额分析</h1>
      </div>
      <div id="twochart" style="height:300px;width:98%;"></div>
      <!-- table -->
      <div class="txalE padd-10"><EXport :ex_data='ex_data2' class="dslb"></EXport></div>
      <Table :tableData='tableData2' :table_title='table_title2' :proplist='proplist2' :table_height='table_height'></Table>
  </div>
</template>

<script>
import Table from '../../../components/table';
import EXport from '../../../components/export';
export default {
  components:{
    Table,EXport
  },
  data(){
    return {
      v_flag:true,
      value1:[new Date(new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 30)),new Date()],
      tableData: [],
      tableData2: [],
      table_title:['序号','时间','销售单数（笔）','商品数量（件）','销售总额','利润(元)','利润率（%）'],
      proplist1:['index','check_date','order_no','goods_num','revenue','re_co','re_co_re'],
      table_title2:['序号','时间','销售总额','销售单数','平均每单商品数','商品均价'],
      proplist2:['index','check_date','revenue','order_no','good_or','re_go'],
      table_height:'190',
      ex_data1:{},
      ex_data2:{},
    }
  },
  watch: {
    'tableData':function (n,o) {
      this.ex_data1={tHeader:this.table_title,filterVal:this.proplist1,tableData:this.tableData,title:'销售趋势分析'}
    },
    'tableData2':function (n,o) {
      this.ex_data2={tHeader:this.table_title2,filterVal:this.proplist2,tableData:this.tableData2,title:'销售总额分析'}
    }
  },
  beforeCreate(){
    // let start=new Date();
    // this.value1=[new Date().setTime(start.getTime() - 3600 * 1000 * 24 * 30),new Date()];
  },
  created() {

    this.getdata()
  },
  mounted(){

  },
  methods:{
    getdata(){
      let data={
        start_date:this.value1?this.value1[0]:'',
        end_date:this.value1?this.value1[1]:'',
      }
      this.$api.salestrendinganalysis(data,res=>{
        console.log(res)
        this.tableData=res.data[0].list
        this.tableData2=res.data[0].list
        for(let index in this.tableData){
          this.tableData[index].re_co=this.tableData[index].revenue - this.tableData[index].cost_basic
          this.tableData[index].re_co_re=this.tableData[index].revenue>0?((this.tableData[index].revenue - this.tableData[index].cost_basic)/this.tableData[index].revenue*100).toFixed(0)+'%':'0%'
        }
        for(let index in this.tableData2){
          this.tableData2[index].good_or=this.tableData2[index].order_no>0?(this.tableData2[index].goods_num/this.tableData2[index].order_no).toFixed(0):0
          this.tableData2[index].re_go=this.tableData2[index].goods_num>0?(Number(this.tableData2[index].revenue)/this.tableData2[index].goods_num).toFixed(2):0
        }
        this.chart()
      })
    },
    chart(){
      let x_data_1=[]
      let y_data_1=[]
      let y_data_2=[]
      let y_data_3=[]
      let y_data_4=[]
      let y_data_5=[]
      let y_data=[y_data_1,y_data_2,y_data_3,y_data_4,y_data_5]
      for(let item of this.tableData){
        x_data_1.push(item.check_date)
        y_data_1.push(Number(item.revenue))
        y_data_2.push(item.order_no)
        y_data_3.push(item.goods_num)
        y_data_4.push(item.re_co)
        y_data_5.push(Number(item.re_co_re.slice(0,item.re_co_re.length-1)))
      }
      this.$chart.bar1('onechart',['销售总额','销售单数','商品数量','利润','毛利率(%)'],x_data_1,y_data);
      let y_data2_3=[]
      let y_data2_4=[]
      let y_data2=[y_data_1,y_data_2,y_data2_3,y_data2_4]
      for(let item of this.tableData2){
        y_data2_3.push(item.good_or)
        y_data2_4.push(item.re_go)
      }
      this.$chart.bar3('twochart',['销售总额','销售单数','平均每单商品数','商品均价'],x_data_1,y_data2);
    }
  }
}
</script>

<style lang="less" scope>
.mg30{
  margin-right: 30px;
}
</style>





