<template>
  <div id="over_all_view_box">
    <!-- search -->
    <div class="txal mg-10">
      <el-autocomplete
        popper-class="my-autocomplete"
        v-model="inputName"
        :fetch-suggestions="querySearch"
        placeholder="请输入内容"
        @select="handleSelect">
        <i
          class="el-icon-edit el-input__icon"
          slot="suffix"
          @click="handleIconClick">
        </i>
        <template slot-scope="{ item }">
          <div class="name">{{ item.value }}</div>
          <span class="addr">{{ item.address }}</span>
        </template>
      </el-autocomplete>
    </div>
    <!-- connent -->
    <div class="fsa mg-10">
      <!--one  -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10">日常数据</h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="#">营业概况</router-link>
          <router-link class="rout_lin txal" to="#">日结记录</router-link>
          <router-link class="rout_lin txal" to="/sale/record/class">交班记录</router-link>
          <router-link class="rout_lin txal" to="#">现金收支</router-link>
          <router-link class="rout_lin txal" to="#">操作日志</router-link>
        </div>
      </div>
      <!-- two -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10">销售报表</h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/sale/data/trend">营业趋势分析</router-link>
          <router-link class="rout_lin txal" to="/sale/data/proportion">营业占比分析</router-link>
          <router-link class="rout_lin txal" to="/sale/data/sale">商品销售统计</router-link>
          <router-link class="rout_lin txal" to="/sale/data/one">笔单价分析</router-link>
          <router-link class="rout_lin txal" to="#">折扣分析</router-link>
        </div>
      </div>
    </div>
    <!-- connent 2-->
    <div class="fsa mg-10">
      <!-- three -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10">库存报表</h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/shopping/overview">商品概览</router-link>
          <router-link class="rout_lin txal" to="/shopping/store/chart">库存查询</router-link>
          <router-link class="rout_lin txal" to="#">库存预警</router-link>
          <router-link class="rout_lin txal" to="#">变动细节</router-link>
          <router-link class="rout_lin txal" to="#">盘点历史</router-link>
        </div>
      </div>
      <!-- fore -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10">会员报表</h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/member/allview">会员概览</router-link>
          <router-link class="rout_lin txal" to="/member/table/massge">会员消费信息分析</router-link>
          <router-link class="rout_lin txal" to="/member/table/once">客单价分析</router-link>
          <router-link class="rout_lin txal" to="/member/table/scor">积分分布</router-link>
        </div>
      </div>
    </div>
    <!-- connent 3-->
    <div class="fsa mg-10">
      <!-- five -->
      <div class="" style="width:40%;border:1px solid #666;height: 212px;">
        <h6 class="txal mg-10">员工报表</h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/staff/staffCen/create">员工管理</router-link>
          <router-link class="rout_lin txal" to="/staff/staffCen/succ">收银员业绩</router-link>
        </div>
      </div>
      <!-- six -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10">员工报表</h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="#">营运分析</router-link>
          <router-link class="rout_lin txal" to="#">销利趋势分析</router-link>
          <router-link class="rout_lin txal" to="#">营业额与毛利率</router-link>
          <router-link class="rout_lin txal" to="#">交易量与票单价</router-link>
          <router-link class="rout_lin txal" to="#">成本与成本率</router-link>
          <router-link class="rout_lin txal" to="#">高峰期趋势分析</router-link>
          <router-link class="rout_lin txal" to="#">月汇总报表</router-link>
          <router-link class="rout_lin txal" to="#">周报</router-link>
          <router-link class="rout_lin txal" to="#">分类销售概览</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    data() {
      return {
        restaurants: [],
        inputName: ''
      };
    },
    methods: {
      querySearch(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          // console.log(restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()))
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1);
        };
      },
      loadAll() {
        return [
          { "value": "营业概况", "router": "/sale/view" },
          { "value": "日结记录", "router": "/sale/record/day" },
          { "value": "交班记录", "router": "/sale/record/class" },
          { "value": "现金收支", "router": "/" },
          { "value": "操作日志", "router": "/" },
          { "value": "营业趋势分析", "router": "/sale/data/trend" },
          { "value": "营业占比分析", "router": "/sale/data/proportion" },
          { "value": "商品销售统计", "router": "/sale/data/sale" },
          { "value": "笔单价分析", "router": "/sale/data/one" },
          { "value": "折扣分析", "router": "/" },
          { "value": "商品概览", "router": "/shopping/overview" },
          { "value": "库存查询", "router": "/shopping/store/chart" },
          { "value": "库存预警", "router": "/shopping/store/del" },
          { "value": "变动明细", "router": "/shopping/store/change" },
          { "value": "盘点历史", "router": "/shopping/store/history" },
          { "value": "会员概览", "router": "/member/allview" },
          { "value": "会员消费信息分析", "router": "/member/table/massge" },
          { "value": "客单价分析", "router": "/member/table/once" },
          { "value": "积分分布", "router": "/member/table/scor" },
          { "value": "员工管理", "router": "/staff/staffCen/create" },
          { "value": "收银员业绩", "router": "/staff/staffCen/succ" },
          { "value": "营运分析", "router": "/" },
          { "value": "销利趋势分析", "router": "/" },
          { "value": "营业额与毛利率", "router": "/" },
          { "value": "交易量与票单价", "router": "/" },
          { "value": "成本与成本率", "router": "/" },
          { "value": "高峰期趋势分析", "router": "/" },
          { "value": "月汇总报表", "router": "/" },
          { "value": "周报", "router": "/" },
          { "value": "分类销售概览", "router": "/" },
        ];
      },
      handleSelect(item) {
        // console.log(item.router);
        var rout=item.router;
        this.$router.push(rout)
      },
      handleIconClick(ev) {
        console.log(ev);
      }
    },
    mounted() {
      this.restaurants = this.loadAll();
    }
}
</script>

<style lang="less" scoped>
#over_all_view_box{
  .my-autocomplete {
    li {
      line-height: normal;
      padding: 7px;

      .name {
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .addr {
        font-size: 12px;
        color: #b4b4b4;
      }

      .highlighted .addr {
        color: #ddd;
      }
    }
  }
  .rout_lin{
    width: 32%;
    display: inline-block;
  }
  a{
    color: cornflowerblue;
  }
}

</style>




